import { useState, useEffect, useCallback, useRef } from 'react';
import { db, getPendingSyncCount, SyncQueueItem } from './offline-db';
import { supabase } from './supabase';
import { useAppStore, Task, Member, Team, ScoutingReport, ChecklistItem, MatchPlan, PortfolioEntry, Season } from './store';

export type SyncStatus = 'idle' | 'syncing' | 'error' | 'offline';

interface UseSyncResult {
    isOnline: boolean;
    syncStatus: SyncStatus;
    pendingChanges: number;
    lastSyncTime: Date | null;
    sync: () => Promise<void>;
    error: string | null;
}

// Table name mapping from local to Supabase
const TABLE_MAP: Record<string, string> = {
    'tasks': 'tasks',
    'scoutingReports': 'scouting_reports',
    'matchPlans': 'match_plans',
    'members': 'members',
    'teams': 'teams',
    'checklist': 'checklist_items',
    'portfolioHistory': 'portfolio_history',
    'seasons': 'seasons',
};

// Transform local data to Supabase format
function toSupabaseFormat(tableName: string, data: any, userId: string): any {
    const baseData = { user_id: userId, updated_at: new Date().toISOString() };

    switch (tableName) {
        case 'tasks':
            return {
                ...baseData,
                id: data.id,
                season_id: data.seasonId || null,
                title: data.title,
                description: data.description || '',
                status: data.status,
                type: data.type,
                assigned_to: data.assignedTo || '',
                department: data.department || '',
                tags: data.tags || [],
                checklist: data.checklist || [],
                timeline: data.timeline || [],
                due_date: data.dueDate ? new Date(data.dueDate).toISOString() : null,
                created_at: new Date(data.createdAt).toISOString(),
            };
        case 'scoutingReports':
            return {
                ...baseData,
                id: data.id,
                season_id: data.seasonId || null,
                team_number: data.teamNumber,
                match_number: data.matchNumber,
                has_autonomous: data.hasAutonomous,
                auto_score: data.autoScore,
                intake_type: data.intakeType,
                auto_aim: data.autoAim,
                far_shooting: data.farShooting,
                shots_taken: data.shotsTaken,
                shots_missed: data.shotsMissed,
                parking: data.parking,
                rating: data.rating,
                end_game_notes: data.endGameNotes,
            };
        case 'matchPlans':
            return {
                ...baseData,
                id: data.id,
                season_id: data.seasonId || null,
                title: data.title,
                drawing_data: data.drawingData || {},
                notes: data.notes || '',
                alliance_team: data.allianceTeam || '',
                partner_autonomous: data.partnerAutonomous || false,
                partner_park: data.partnerPark || false,
            };
        case 'checklist':
            return {
                ...baseData,
                id: data.id,
                season_id: data.seasonId || null,
                text: data.text,
                checked: data.checked,
                assigned_to: data.assignedTo || '',
            };
        case 'members':
            return {
                ...baseData,
                id: data.id,
                season_id: data.seasonId || null,
                first_name: data.firstName,
                last_name_initial: data.lastNameInitial,
            };
        case 'teams':
            return {
                ...baseData,
                id: data.id,
                season_id: data.seasonId || null,
                name: data.name,
                member_ids: data.memberIds || [],
            };
        case 'portfolioHistory':
            return {
                ...baseData,
                id: data.id,
                season_id: data.seasonId || null,
                content: data.content,
                task_count: data.taskCount,
                created_at: new Date(data.createdAt).toISOString(),
            };
        case 'seasons':
            return {
                ...baseData,
                id: data.id,
                name: data.name,
                field_image_url: data.fieldImageUrl || '',
                created_at: new Date(data.createdAt).toISOString(),
            };
        default:
            return { ...baseData, ...data };
    }
}

// Transform Supabase data to local format
function fromSupabaseFormat(tableName: string, data: any): any {
    switch (tableName) {
        case 'tasks':
            return {
                id: data.id,
                seasonId: data.season_id,
                title: data.title,
                description: data.description,
                status: data.status,
                type: data.type,
                assignedTo: data.assigned_to,
                department: data.department,
                tags: data.tags || [],
                checklist: data.checklist || [],
                timeline: data.timeline || [],
                dueDate: data.due_date ? new Date(data.due_date).getTime() : undefined,
                createdAt: new Date(data.created_at).getTime(),
            } as Task;
        case 'scouting_reports':
            return {
                id: data.id,
                seasonId: data.season_id,
                teamNumber: data.team_number,
                matchNumber: data.match_number,
                hasAutonomous: data.has_autonomous,
                autoScore: data.auto_score,
                intakeType: data.intake_type,
                autoAim: data.auto_aim,
                farShooting: data.far_shooting,
                shotsTaken: data.shots_taken,
                shotsMissed: data.shots_missed,
                parking: data.parking,
                rating: data.rating,
                endGameNotes: data.end_game_notes,
            } as ScoutingReport;
        case 'match_plans':
            return {
                id: data.id,
                seasonId: data.season_id,
                title: data.title,
                drawingData: data.drawing_data,
                notes: data.notes,
                allianceTeam: data.alliance_team,
                partnerAutonomous: data.partner_autonomous,
                partnerPark: data.partner_park,
                updatedAt: new Date(data.updated_at).getTime(),
            } as MatchPlan;
        case 'checklist_items':
            return {
                id: data.id,
                seasonId: data.season_id,
                text: data.text,
                checked: data.checked,
                assignedTo: data.assigned_to,
            } as ChecklistItem;
        case 'members':
            return {
                id: data.id,
                seasonId: data.season_id,
                firstName: data.first_name,
                lastNameInitial: data.last_name_initial,
            } as Member;
        case 'teams':
            return {
                id: data.id,
                seasonId: data.season_id,
                name: data.name,
                memberIds: data.member_ids || [],
            } as Team;
        case 'portfolio_history':
            return {
                id: data.id,
                seasonId: data.season_id,
                content: data.content,
                taskCount: data.task_count,
                createdAt: new Date(data.created_at).getTime(),
            } as PortfolioEntry;
        case 'seasons':
            return {
                id: data.id,
                name: data.name,
                fieldImageUrl: data.field_image_url,
                createdAt: new Date(data.created_at).getTime(),
            } as Season;
        default:
            return data;
    }
}

export function useSync(): UseSyncResult {
    const [isOnline, setIsOnline] = useState(navigator.onLine);
    const [syncStatus, setSyncStatus] = useState<SyncStatus>('idle');
    const [pendingChanges, setPendingChanges] = useState(0);
    const [lastSyncTime, setLastSyncTime] = useState<Date | null>(null);
    const [error, setError] = useState<string | null>(null);
    const syncingRef = useRef(false);

    // Track online/offline status
    useEffect(() => {
        const handleOnline = () => {
            setIsOnline(true);
            setSyncStatus('idle');
        };

        const handleOffline = () => {
            setIsOnline(false);
            setSyncStatus('offline');
            syncingRef.current = false;
        };

        window.addEventListener('online', handleOnline);
        window.addEventListener('offline', handleOffline);

        return () => {
            window.removeEventListener('online', handleOnline);
            window.removeEventListener('offline', handleOffline);
        };
    }, []);

    // Update pending changes count
    useEffect(() => {
        const updatePendingCount = async () => {
            const count = await getPendingSyncCount();
            setPendingChanges(count);
        };

        updatePendingCount();

        // Poll for changes every 5 seconds
        const interval = setInterval(updatePendingCount, 5000);
        return () => clearInterval(interval);
    }, []);

    // Auto-sync when coming back online or when pending changes increase
    useEffect(() => {
        if (isOnline && pendingChanges > 0 && syncStatus === 'idle' && !syncingRef.current) {
            sync();
        }
    }, [isOnline, pendingChanges, syncStatus]);

    const sync = useCallback(async () => {
        if (syncingRef.current) return;
        if (!isOnline || !supabase) {
            setSyncStatus('offline');
            return;
        }

        syncingRef.current = true;
        setSyncStatus('syncing');
        setError(null);

        try {
            // Get current user
            const { data: { user } } = await supabase.auth.getUser();
            if (!user) {
                throw new Error('Not authenticated');
            }

            console.log('[Sync] Starting sync for user:', user.id);

            // Push: Process all pending sync items
            const queueItems = await db.syncQueue.toArray();
            console.log('[Sync] Pending items to sync:', queueItems.length);

            for (const item of queueItems) {
                try {
                    console.log('[Sync] Processing item:', item.tableName, item.operation, item.recordId);
                    await processSyncItem(item, user.id);
                    await db.syncQueue.delete(item.id);
                    console.log('[Sync] Successfully synced item:', item.recordId);
                } catch (err: any) {
                    console.error('[Sync] Failed to sync item:', item.tableName, item.recordId, err?.message || err);
                    const newRetryCount = (item.retryCount || 0) + 1;

                    if (newRetryCount >= 5) {
                        console.error(`[Sync] Item ${item.id} failed after 5 retries. Removing from queue.`);
                        await db.syncQueue.delete(item.id);
                    } else {
                        await db.syncQueue.update(item.id, {
                            retryCount: newRetryCount,
                            lastError: err instanceof Error ? err.message : 'Unknown error',
                        });
                    }
                }
            }

            // Pull: Get latest changes from server
            await pullChangesFromServer(user.id);

            setLastSyncTime(new Date());
            setSyncStatus('idle');

            const count = await getPendingSyncCount();
            setPendingChanges(count);
        } catch (err) {
            console.error('Sync failed:', err);
            setError(err instanceof Error ? err.message : 'Sync failed');
            setSyncStatus('error');
        } finally {
            syncingRef.current = false;
        }
    }, [isOnline]);

    return {
        isOnline,
        syncStatus,
        pendingChanges,
        lastSyncTime,
        sync,
        error,
    };
}

async function processSyncItem(item: SyncQueueItem, userId: string): Promise<void> {
    if (!supabase) throw new Error('Supabase not configured');

    const { tableName, operation, data, recordId } = item;
    const supabaseTable = TABLE_MAP[tableName] || tableName;

    switch (operation) {
        case 'create':
            const createData = toSupabaseFormat(tableName, data, userId);
            const { error: createError } = await supabase
                .from(supabaseTable)
                .upsert(createData, { onConflict: 'id' }); // Use upsert for idempotency
            if (createError) throw createError;
            break;

        case 'update':
            const updateData = toSupabaseFormat(tableName, data, userId);
            const { error: updateError } = await (supabase
                .from(supabaseTable) as any)
                .update(updateData)
                .eq('id', recordId);
            if (updateError) throw updateError;
            break;

        case 'delete':
            const { error: deleteError } = await supabase
                .from(supabaseTable)
                .delete()
                .eq('id', recordId);
            if (deleteError) throw deleteError;
            break;
    }
}

async function pullChangesFromServer(userId: string): Promise<void> {
    if (!supabase) return;

    try {
        // Fetch all data from server
        const [
            { data: tasks },
            { data: scoutingReports },
            { data: matchPlans },
            { data: checklistItems },
            { data: members },
            { data: teams },
            { data: portfolioHistory },
            { data: seasons },
        ] = await Promise.all([
            supabase.from('tasks').select('*').eq('user_id', userId),
            supabase.from('scouting_reports').select('*').eq('user_id', userId),
            supabase.from('match_plans').select('*').eq('user_id', userId),
            supabase.from('checklist_items').select('*').eq('user_id', userId),
            supabase.from('members').select('*').eq('user_id', userId),
            supabase.from('teams').select('*').eq('user_id', userId),
            supabase.from('portfolio_history').select('*').eq('user_id', userId),
            supabase.from('seasons').select('*').eq('user_id', userId),
        ]);

        // Merge with local data using last-write-wins
        // Server data takes precedence since it's the source of truth after push
        if (tasks && tasks.length > 0) {
            useAppStore.setState({ tasks: tasks.map(t => fromSupabaseFormat('tasks', t)) });
        }

        if (scoutingReports && scoutingReports.length > 0) {
            useAppStore.setState({ scoutingReports: scoutingReports.map(r => fromSupabaseFormat('scouting_reports', r)) });
        }

        if (matchPlans && matchPlans.length > 0) {
            useAppStore.setState({ matchPlans: matchPlans.map(p => fromSupabaseFormat('match_plans', p)) });
        }

        if (checklistItems && checklistItems.length > 0) {
            useAppStore.setState({ checklist: checklistItems.map(c => fromSupabaseFormat('checklist_items', c)) });
        }

        if (members && members.length > 0) {
            useAppStore.setState({ members: members.map(m => fromSupabaseFormat('members', m)) });
        }

        if (teams && teams.length > 0) {
            useAppStore.setState({ teams: teams.map(t => fromSupabaseFormat('teams', t)) });
        }

        if (portfolioHistory && portfolioHistory.length > 0) {
            useAppStore.setState({ portfolioHistory: portfolioHistory.map(p => fromSupabaseFormat('portfolio_history', p)) });
        }

        if (seasons && seasons.length > 0) {
            useAppStore.setState({ seasons: seasons.map(s => fromSupabaseFormat('seasons', s)) });
        }

        console.log('Pull from server completed');
    } catch (err) {
        console.error('Failed to pull changes from server:', err);
        throw err;
    }
}

// Load user data from server (called on login)
export async function loadUserDataFromServer(): Promise<void> {
    if (!supabase) return;

    const { data: { user } } = await supabase.auth.getUser();
    if (!user) return;

    // First, sync any local default data that doesn't exist on server
    await initializeUserData(user.id);

    // Then pull from server
    await pullChangesFromServer(user.id);
}

// Initialize default data for new users
async function initializeUserData(userId: string): Promise<void> {
    if (!supabase) return;

    const store = useAppStore.getState();

    // Check if user has any seasons in Supabase
    const { data: existingSeasons } = await supabase
        .from('seasons')
        .select('id')
        .eq('user_id', userId)
        .limit(1);

    // If no seasons exist, push local seasons to Supabase
    if (!existingSeasons || existingSeasons.length === 0) {
        console.log('Initializing default data for new user...');

        // Push local seasons
        for (const season of store.seasons) {
            const seasonData = toSupabaseFormat('seasons', season, userId);
            const { error } = await supabase.from('seasons').upsert(seasonData, { onConflict: 'id' });
            if (error) {
                console.error('Failed to initialize season:', error);
            }
        }

        // Push local teams
        for (const team of store.teams) {
            const teamData = toSupabaseFormat('teams', team, userId);
            const { error } = await supabase.from('teams').upsert(teamData, { onConflict: 'id' });
            if (error) {
                console.error('Failed to initialize team:', error);
            }
        }

        // Push local checklist items
        for (const item of store.checklist) {
            const itemData = toSupabaseFormat('checklist', item, userId);
            const { error } = await supabase.from('checklist_items').upsert(itemData, { onConflict: 'id' });
            if (error) {
                console.error('Failed to initialize checklist item:', error);
            }
        }

        console.log('Default data initialization complete');
    }
}

// Hook to detect if user is offline
export function useOnlineStatus(): boolean {
    const [isOnline, setIsOnline] = useState(navigator.onLine);

    useEffect(() => {
        const handleOnline = () => setIsOnline(true);
        const handleOffline = () => setIsOnline(false);

        window.addEventListener('online', handleOnline);
        window.addEventListener('offline', handleOffline);

        return () => {
            window.removeEventListener('online', handleOnline);
            window.removeEventListener('offline', handleOffline);
        };
    }, []);

    return isOnline;
}
