import { useState } from 'react';
import { User, Save, X, Mail, Shield } from 'lucide-react';
import { useAuth } from '../lib/auth';
import { useOrganization } from '../lib/organization-context';

export default function UserProfile() {
    const { user, updateProfile, isConfigured } = useAuth();
    const { currentMembership } = useOrganization();

    const [isEditingName, setIsEditingName] = useState(false);
    const [editDisplayName, setEditDisplayName] = useState('');
    const [isSaving, setIsSaving] = useState(false);
    const [message, setMessage] = useState<{ type: 'success' | 'error'; text: string } | null>(null);

    const handleSaveName = async () => {
        if (!editDisplayName.trim()) return;

        setIsSaving(true);
        setMessage(null);

        const { error } = await updateProfile(editDisplayName.trim());

        if (error) {
            setMessage({ type: 'error', text: error.message });
        } else {
            setMessage({ type: 'success', text: 'Name updated successfully!' });
            setIsEditingName(false);
        }
        setIsSaving(false);
    };

    if (!isConfigured || !user) {
        return (
            <div className="p-3 md:p-6 max-w-2xl mx-auto">
                <div className="bg-white dark:bg-slate-800 rounded-xl shadow-sm border border-slate-200 dark:border-slate-700 p-6 text-center">
                    <p className="text-slate-500 dark:text-slate-400">
                        Please sign in to view your profile.
                    </p>
                </div>
            </div>
        );
    }

    const roleLabels: Record<string, string> = {
        owner: 'Team Owner',
        coach: 'Coach',
        mentor: 'Mentor',
        student: 'Student',
    };

    return (
        <div className="p-3 md:p-6 max-w-2xl mx-auto w-full h-full overflow-y-auto">
            <h2 className="text-2xl font-bold text-slate-800 dark:text-white mb-6">Your Profile</h2>

            {/* Message Display */}
            {message && (
                <div className={`mb-6 p-4 rounded-xl text-sm ${message.type === 'success'
                    ? 'bg-green-50 dark:bg-green-900/20 text-green-700 dark:text-green-400 border border-green-200 dark:border-green-800'
                    : 'bg-red-50 dark:bg-red-900/20 text-red-700 dark:text-red-400 border border-red-200 dark:border-red-800'
                    }`}>
                    {message.text}
                </div>
            )}

            {/* Profile Card */}
            <div className="bg-white dark:bg-slate-800 rounded-xl shadow-sm border border-slate-200 dark:border-slate-700 overflow-hidden">
                {/* Header */}
                <div className="bg-gradient-to-r from-orange-500 to-amber-500 p-6">
                    <div className="flex items-center gap-4">
                        <div className="w-20 h-20 rounded-full bg-white/20 flex items-center justify-center">
                            <User size={40} className="text-white" />
                        </div>
                        <div>
                            <h3 className="text-xl font-bold text-white">
                                {user.user_metadata?.full_name || 'No name set'}
                            </h3>
                            <p className="text-orange-100">{user.email}</p>
                        </div>
                    </div>
                </div>

                {/* Details */}
                <div className="p-6 space-y-6">
                    {/* Name Section */}
                    <div>
                        <label className="text-sm font-medium text-slate-500 dark:text-slate-400 flex items-center gap-2 mb-2">
                            <User size={16} />
                            Display Name
                        </label>
                        {isEditingName ? (
                            <div className="flex gap-2 items-center">
                                <input
                                    type="text"
                                    value={editDisplayName}
                                    onChange={(e) => setEditDisplayName(e.target.value)}
                                    placeholder="Enter your name"
                                    className="flex-1 p-3 rounded-lg border border-slate-300 dark:border-slate-600 bg-slate-50 dark:bg-slate-700 text-slate-900 dark:text-white"
                                    onKeyDown={(e) => e.key === 'Enter' && handleSaveName()}
                                    autoFocus
                                />
                                <button
                                    onClick={handleSaveName}
                                    disabled={isSaving || !editDisplayName.trim()}
                                    className="bg-orange-600 text-white p-3 rounded-lg hover:bg-orange-700 transition disabled:opacity-50"
                                >
                                    <Save size={18} />
                                </button>
                                <button
                                    onClick={() => {
                                        setIsEditingName(false);
                                        setMessage(null);
                                    }}
                                    className="text-slate-400 hover:text-slate-600 dark:hover:text-slate-300 p-3"
                                >
                                    <X size={18} />
                                </button>
                            </div>
                        ) : (
                            <div className="flex items-center justify-between">
                                <p className="text-lg text-slate-800 dark:text-white">
                                    {user.user_metadata?.full_name || 'Not set'}
                                </p>
                                <button
                                    onClick={() => {
                                        setEditDisplayName(user.user_metadata?.full_name || '');
                                        setIsEditingName(true);
                                        setMessage(null);
                                    }}
                                    className="px-4 py-2 text-sm font-medium text-slate-600 dark:text-slate-300 bg-slate-100 dark:bg-slate-700 rounded-lg hover:bg-slate-200 dark:hover:bg-slate-600 transition"
                                >
                                    Edit Name
                                </button>
                            </div>
                        )}
                    </div>

                    {/* Email Section */}
                    <div>
                        <label className="text-sm font-medium text-slate-500 dark:text-slate-400 flex items-center gap-2 mb-2">
                            <Mail size={16} />
                            Email Address
                        </label>
                        <p className="text-lg text-slate-800 dark:text-white">{user.email}</p>
                        <p className="text-xs text-slate-400 mt-1">Email cannot be changed</p>
                    </div>

                    {/* Role Section */}
                    {currentMembership && (
                        <div>
                            <label className="text-sm font-medium text-slate-500 dark:text-slate-400 flex items-center gap-2 mb-2">
                                <Shield size={16} />
                                Current Team Role
                            </label>
                            <p className="text-lg text-slate-800 dark:text-white">
                                {roleLabels[currentMembership.role] || currentMembership.role}
                            </p>
                        </div>
                    )}
                </div>
            </div>
        </div>
    );
}
