import React, { useState } from 'react';
import { useAuth } from '../lib/auth';
import { Mail, Lock, User, ArrowRight, Loader2, Shield, Users, CheckCircle2, AlertCircle } from 'lucide-react';

type AuthMode = 'login' | 'signup-choice' | 'signup-coach' | 'signup-student' | 'forgot';

export default function LoginPage() {
    const { signInWithEmail, signUpAsCoach, signUpAsStudentOrMentor, resetPassword } = useAuth();
    const [mode, setMode] = useState<AuthMode>('login');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [fullName, setFullName] = useState('');
    const [inviteCode, setInviteCode] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const [message, setMessage] = useState<string | null>(null);

    // Coach signup checkboxes
    const [confirmedAge18Plus, setConfirmedAge18Plus] = useState(false);
    const [acceptedTerms, setAcceptedTerms] = useState(false);
    const [acknowledgedCoppa, setAcknowledgedCoppa] = useState(false);

    // Student signup checkboxes
    const [confirmedAge13Plus, setConfirmedAge13Plus] = useState(false);
    const [acknowledgedPrivacy, setAcknowledgedPrivacy] = useState(false);
    const [acknowledgedGuidelines, setAcknowledgedGuidelines] = useState(false);

    const resetForm = () => {
        setEmail('');
        setPassword('');
        setFullName('');
        setInviteCode('');
        setConfirmedAge18Plus(false);
        setAcceptedTerms(false);
        setAcknowledgedCoppa(false);
        setConfirmedAge13Plus(false);
        setAcknowledgedPrivacy(false);
        setAcknowledgedGuidelines(false);
        setError(null);
        setMessage(null);
    };

    const handleEmailAuth = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsLoading(true);
        setError(null);
        setMessage(null);

        try {
            if (mode === 'login') {
                const { error } = await signInWithEmail(email, password);
                if (error) setError(error.message);
            } else if (mode === 'signup-coach') {
                const { error } = await signUpAsCoach({
                    email,
                    password,
                    fullName,
                    confirmedAge18Plus,
                    acceptedTerms,
                    acknowledgedCoppaResponsibility: acknowledgedCoppa,
                });
                if (error) {
                    setError(error.message);
                } else {
                    setMessage('Account created! Check your email for a confirmation link.');
                }
            } else if (mode === 'signup-student') {
                const { error } = await signUpAsStudentOrMentor({
                    email,
                    password,
                    fullName,
                    confirmedAge13Plus,
                    acknowledgedPrivacyPolicy: acknowledgedPrivacy,
                    acknowledgedCommunityGuidelines: acknowledgedGuidelines,
                    inviteCode: inviteCode || undefined,
                });
                if (error) {
                    setError(error.message);
                } else {
                    setMessage('Account created! Check your email for a confirmation link.');
                }
            } else if (mode === 'forgot') {
                const { error } = await resetPassword(email);
                if (error) {
                    setError(error.message);
                } else {
                    setMessage('Password reset email sent!');
                }
            }
        } catch (err) {
            setError('An unexpected error occurred');
        } finally {
            setIsLoading(false);
        }
    };

    const getTitle = () => {
        switch (mode) {
            case 'login': return 'Sign in to your account';
            case 'signup-choice': return 'Create your account';
            case 'signup-coach': return 'Coach Registration';
            case 'signup-student': return 'Student / Mentor Registration';
            case 'forgot': return 'Reset your password';
        }
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center p-4">
            <div className="max-w-md w-full">
                {/* Logo & Title */}
                <div className="text-center mb-8">
                    <div className="inline-flex items-center justify-center w-20 h-20 bg-gradient-to-br from-slate-800 to-slate-900 rounded-3xl shadow-xl border border-slate-700/50 mb-4 p-4">
                        <img
                            src={`${import.meta.env.BASE_URL}falcon_logo.png`}
                            className="w-full h-full object-contain"
                            alt="FalconForge Logo"
                        />
                    </div>
                    <h1 className="text-3xl font-black italic tracking-tighter mb-2">
                        <span className="bg-gradient-to-r from-orange-500 to-amber-500 bg-clip-text text-transparent">FALCON</span>
                        <span className="text-white">FORGE</span>
                    </h1>
                    <p className="text-slate-400">{getTitle()}</p>
                </div>

                {/* Auth Card */}
                <div className="bg-slate-800/50 backdrop-blur-xl border border-slate-700 rounded-2xl p-6 shadow-xl">
                    {/* Error/Message Display */}
                    {error && (
                        <div className="bg-red-500/10 border border-red-500/20 rounded-xl p-3 mb-4 flex items-start gap-2">
                            <AlertCircle className="w-5 h-5 text-red-400 flex-shrink-0 mt-0.5" />
                            <p className="text-red-400 text-sm">{error}</p>
                        </div>
                    )}
                    {message && (
                        <div className="bg-green-500/10 border border-green-500/20 rounded-xl p-3 mb-4 flex items-start gap-2">
                            <CheckCircle2 className="w-5 h-5 text-green-400 flex-shrink-0 mt-0.5" />
                            <p className="text-green-400 text-sm">{message}</p>
                        </div>
                    )}

                    {/* Signup Choice Screen */}
                    {mode === 'signup-choice' && (
                        <div className="space-y-4">
                            <p className="text-slate-300 text-sm text-center mb-4">
                                Choose your account type
                            </p>

                            {/* Coach Option */}
                            <button
                                onClick={() => { resetForm(); setMode('signup-coach'); }}
                                className="w-full p-4 bg-slate-700/50 border border-slate-600 rounded-xl hover:border-orange-500/50 hover:bg-slate-700 transition-all text-left group"
                            >
                                <div className="flex items-center gap-3 mb-2">
                                    <div className="w-10 h-10 rounded-xl bg-orange-500/20 flex items-center justify-center">
                                        <Shield className="w-5 h-5 text-orange-400" />
                                    </div>
                                    <div>
                                        <h3 className="text-white font-semibold group-hover:text-orange-400 transition-colors">Coach</h3>
                                        <p className="text-slate-400 text-xs">Adults 18+ who manage teams</p>
                                    </div>
                                </div>
                                <p className="text-slate-500 text-xs pl-[52px]">
                                    Create teams, invite members, manage billing
                                </p>
                            </button>

                            {/* Student/Mentor Option */}
                            <button
                                onClick={() => { resetForm(); setMode('signup-student'); }}
                                className="w-full p-4 bg-slate-700/50 border border-slate-600 rounded-xl hover:border-blue-500/50 hover:bg-slate-700 transition-all text-left group"
                            >
                                <div className="flex items-center gap-3 mb-2">
                                    <div className="w-10 h-10 rounded-xl bg-blue-500/20 flex items-center justify-center">
                                        <Users className="w-5 h-5 text-blue-400" />
                                    </div>
                                    <div>
                                        <h3 className="text-white font-semibold group-hover:text-blue-400 transition-colors">Student / Mentor</h3>
                                        <p className="text-slate-400 text-xs">Join an existing team</p>
                                    </div>
                                </div>
                                <p className="text-slate-500 text-xs pl-[52px]">
                                    Requires invite from a coach
                                </p>
                            </button>

                            <div className="pt-4 text-center">
                                <p className="text-slate-400 text-sm">
                                    Already have an account?{' '}
                                    <button
                                        onClick={() => { resetForm(); setMode('login'); }}
                                        className="text-orange-400 hover:text-orange-300 font-medium"
                                    >
                                        Sign in
                                    </button>
                                </p>
                            </div>
                        </div>
                    )}

                    {/* Login Form */}
                    {mode === 'login' && (
                        <form onSubmit={handleEmailAuth} className="space-y-4">
                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">Email</label>
                                <div className="relative">
                                    <Mail className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                                    <input
                                        type="email"
                                        value={email}
                                        onChange={(e) => setEmail(e.target.value)}
                                        className="w-full pl-10 pr-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                                        placeholder="you@team12345.org"
                                        required
                                    />
                                </div>
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">Password</label>
                                <div className="relative">
                                    <Lock className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                                    <input
                                        type="password"
                                        value={password}
                                        onChange={(e) => setPassword(e.target.value)}
                                        className="w-full pl-10 pr-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                                        placeholder=""
                                        required
                                        minLength={6}
                                    />
                                </div>
                            </div>

                            <button
                                type="submit"
                                disabled={isLoading}
                                className="w-full flex items-center justify-center gap-2 bg-gradient-to-r from-orange-500 to-orange-600 text-white font-semibold py-3 px-4 rounded-xl hover:from-orange-600 hover:to-orange-700 transition-all shadow-lg shadow-orange-500/25 disabled:opacity-50"
                            >
                                {isLoading ? (
                                    <Loader2 className="w-5 h-5 animate-spin" />
                                ) : (
                                    <>
                                        Sign In
                                        <ArrowRight className="w-4 h-4" />
                                    </>
                                )}
                            </button>

                            <div className="text-center text-sm space-y-2">
                                <button
                                    type="button"
                                    onClick={() => { resetForm(); setMode('forgot'); }}
                                    className="text-slate-400 hover:text-orange-400 transition-colors"
                                >
                                    Forgot password?
                                </button>
                                <p className="text-slate-400">
                                    Don't have an account?{' '}
                                    <button
                                        type="button"
                                        onClick={() => { resetForm(); setMode('signup-choice'); }}
                                        className="text-orange-400 hover:text-orange-300 font-medium"
                                    >
                                        Sign up
                                    </button>
                                </p>
                            </div>
                        </form>
                    )}

                    {/* Coach Signup Form */}
                    {mode === 'signup-coach' && (
                        <form onSubmit={handleEmailAuth} className="space-y-4">
                            <div className="bg-orange-500/10 border border-orange-500/20 rounded-xl p-3 mb-2">
                                <p className="text-orange-300 text-xs">
                                    <strong>Coach accounts</strong> are for adults (18+) who manage robotics teams. You'll be able to create teams, invite members, and handle billing.
                                </p>
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">Full Name</label>
                                <div className="relative">
                                    <User className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                                    <input
                                        type="text"
                                        value={fullName}
                                        onChange={(e) => setFullName(e.target.value)}
                                        className="w-full pl-10 pr-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                                        placeholder="John Smith"
                                        required
                                    />
                                </div>
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">Email</label>
                                <div className="relative">
                                    <Mail className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                                    <input
                                        type="email"
                                        value={email}
                                        onChange={(e) => setEmail(e.target.value)}
                                        className="w-full pl-10 pr-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                                        placeholder="you@team12345.org"
                                        required
                                    />
                                </div>
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">Password</label>
                                <div className="relative">
                                    <Lock className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                                    <input
                                        type="password"
                                        value={password}
                                        onChange={(e) => setPassword(e.target.value)}
                                        className="w-full pl-10 pr-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                                        placeholder=""
                                        required
                                        minLength={6}
                                    />
                                </div>
                            </div>

                            {/* Compliance Checkboxes */}
                            <div className="space-y-3 pt-2">
                                <label className="flex items-start gap-3 cursor-pointer group">
                                    <input
                                        type="checkbox"
                                        checked={confirmedAge18Plus}
                                        onChange={(e) => setConfirmedAge18Plus(e.target.checked)}
                                        className="mt-1 w-4 h-4 rounded border-slate-500 text-orange-500 focus:ring-orange-500 focus:ring-offset-slate-800"
                                        required
                                    />
                                    <span className="text-sm text-slate-300 group-hover:text-white transition-colors">
                                        I confirm that I am <strong>18 years of age or older</strong>
                                    </span>
                                </label>

                                <label className="flex items-start gap-3 cursor-pointer group">
                                    <input
                                        type="checkbox"
                                        checked={acceptedTerms}
                                        onChange={(e) => setAcceptedTerms(e.target.checked)}
                                        className="mt-1 w-4 h-4 rounded border-slate-500 text-orange-500 focus:ring-orange-500 focus:ring-offset-slate-800"
                                        required
                                    />
                                    <span className="text-sm text-slate-300 group-hover:text-white transition-colors">
                                        I agree to the{' '}
                                        <a href="/#/terms" target="_blank" className="text-orange-400 hover:underline">Terms and Conditions</a>
                                    </span>
                                </label>

                                <label className="flex items-start gap-3 cursor-pointer group">
                                    <input
                                        type="checkbox"
                                        checked={acknowledgedCoppa}
                                        onChange={(e) => setAcknowledgedCoppa(e.target.checked)}
                                        className="mt-1 w-4 h-4 rounded border-slate-500 text-orange-500 focus:ring-orange-500 focus:ring-offset-slate-800"
                                        required
                                    />
                                    <span className="text-sm text-slate-300 group-hover:text-white transition-colors">
                                        I acknowledge that I am responsible for obtaining necessary parental/guardian consent for any minors I invite to my team (COPPA compliance)
                                    </span>
                                </label>
                            </div>

                            <button
                                type="submit"
                                disabled={isLoading || !confirmedAge18Plus || !acceptedTerms || !acknowledgedCoppa}
                                className="w-full flex items-center justify-center gap-2 bg-gradient-to-r from-orange-500 to-orange-600 text-white font-semibold py-3 px-4 rounded-xl hover:from-orange-600 hover:to-orange-700 transition-all shadow-lg shadow-orange-500/25 disabled:opacity-50 disabled:cursor-not-allowed"
                            >
                                {isLoading ? (
                                    <Loader2 className="w-5 h-5 animate-spin" />
                                ) : (
                                    <>
                                        Create Coach Account
                                        <ArrowRight className="w-4 h-4" />
                                    </>
                                )}
                            </button>

                            <p className="text-center text-sm text-slate-400">
                                <button
                                    type="button"
                                    onClick={() => { resetForm(); setMode('signup-choice'); }}
                                    className="text-orange-400 hover:text-orange-300 font-medium"
                                >
                                     Back to account type selection
                                </button>
                            </p>
                        </form>
                    )}

                    {/* Student/Mentor Signup Form */}
                    {mode === 'signup-student' && (
                        <form onSubmit={handleEmailAuth} className="space-y-4">
                            <div className="bg-blue-500/10 border border-blue-500/20 rounded-xl p-3 mb-2">
                                <p className="text-blue-300 text-xs">
                                    <strong>Student/Mentor accounts</strong> are for team members joining via invite. You'll need an invite code from your coach to access team data.
                                </p>
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">Full Name</label>
                                <div className="relative">
                                    <User className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                                    <input
                                        type="text"
                                        value={fullName}
                                        onChange={(e) => setFullName(e.target.value)}
                                        className="w-full pl-10 pr-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                                        placeholder="Jane Doe"
                                        required
                                    />
                                </div>
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">Email</label>
                                <div className="relative">
                                    <Mail className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                                    <input
                                        type="email"
                                        value={email}
                                        onChange={(e) => setEmail(e.target.value)}
                                        className="w-full pl-10 pr-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                                        placeholder="you@email.com"
                                        required
                                    />
                                </div>
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">Password</label>
                                <div className="relative">
                                    <Lock className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                                    <input
                                        type="password"
                                        value={password}
                                        onChange={(e) => setPassword(e.target.value)}
                                        className="w-full pl-10 pr-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                                        placeholder=""
                                        required
                                        minLength={6}
                                    />
                                </div>
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">
                                    Invite Code <span className="text-slate-500">(optional - can add later)</span>
                                </label>
                                <input
                                    type="text"
                                    value={inviteCode}
                                    onChange={(e) => setInviteCode(e.target.value.toUpperCase())}
                                    className="w-full px-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent uppercase tracking-wider"
                                    placeholder="ABC123"
                                />
                            </div>

                            {/* Compliance Checkboxes */}
                            <div className="space-y-3 pt-2">
                                <label className="flex items-start gap-3 cursor-pointer group">
                                    <input
                                        type="checkbox"
                                        checked={confirmedAge13Plus}
                                        onChange={(e) => setConfirmedAge13Plus(e.target.checked)}
                                        className="mt-1 w-4 h-4 rounded border-slate-500 text-blue-500 focus:ring-blue-500 focus:ring-offset-slate-800"
                                        required
                                    />
                                    <span className="text-sm text-slate-300 group-hover:text-white transition-colors">
                                        I confirm that I am <strong>at least 13 years old</strong>
                                    </span>
                                </label>

                                <label className="flex items-start gap-3 cursor-pointer group">
                                    <input
                                        type="checkbox"
                                        checked={acknowledgedPrivacy}
                                        onChange={(e) => setAcknowledgedPrivacy(e.target.checked)}
                                        className="mt-1 w-4 h-4 rounded border-slate-500 text-blue-500 focus:ring-blue-500 focus:ring-offset-slate-800"
                                        required
                                    />
                                    <span className="text-sm text-slate-300 group-hover:text-white transition-colors">
                                        I have read and understand the{' '}
                                        <a href="/#/privacy" target="_blank" className="text-blue-400 hover:underline">Privacy Policy</a>
                                    </span>
                                </label>

                                <label className="flex items-start gap-3 cursor-pointer group">
                                    <input
                                        type="checkbox"
                                        checked={acknowledgedGuidelines}
                                        onChange={(e) => setAcknowledgedGuidelines(e.target.checked)}
                                        className="mt-1 w-4 h-4 rounded border-slate-500 text-blue-500 focus:ring-blue-500 focus:ring-offset-slate-800"
                                        required
                                    />
                                    <span className="text-sm text-slate-300 group-hover:text-white transition-colors">
                                        I have read and will follow the{' '}
                                        <a href="/#/terms" target="_blank" className="text-blue-400 hover:underline">Community Guidelines</a>
                                    </span>
                                </label>
                            </div>

                            <button
                                type="submit"
                                disabled={isLoading || !confirmedAge13Plus || !acknowledgedPrivacy || !acknowledgedGuidelines}
                                className="w-full flex items-center justify-center gap-2 bg-gradient-to-r from-blue-500 to-blue-600 text-white font-semibold py-3 px-4 rounded-xl hover:from-blue-600 hover:to-blue-700 transition-all shadow-lg shadow-blue-500/25 disabled:opacity-50 disabled:cursor-not-allowed"
                            >
                                {isLoading ? (
                                    <Loader2 className="w-5 h-5 animate-spin" />
                                ) : (
                                    <>
                                        Create Account
                                        <ArrowRight className="w-4 h-4" />
                                    </>
                                )}
                            </button>

                            <p className="text-center text-sm text-slate-400">
                                <button
                                    type="button"
                                    onClick={() => { resetForm(); setMode('signup-choice'); }}
                                    className="text-blue-400 hover:text-blue-300 font-medium"
                                >
                                     Back to account type selection
                                </button>
                            </p>
                        </form>
                    )}

                    {/* Forgot Password Form */}
                    {mode === 'forgot' && (
                        <form onSubmit={handleEmailAuth} className="space-y-4">
                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">Email</label>
                                <div className="relative">
                                    <Mail className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                                    <input
                                        type="email"
                                        value={email}
                                        onChange={(e) => setEmail(e.target.value)}
                                        className="w-full pl-10 pr-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                                        placeholder="you@team12345.org"
                                        required
                                    />
                                </div>
                            </div>

                            <button
                                type="submit"
                                disabled={isLoading}
                                className="w-full flex items-center justify-center gap-2 bg-gradient-to-r from-orange-500 to-orange-600 text-white font-semibold py-3 px-4 rounded-xl hover:from-orange-600 hover:to-orange-700 transition-all shadow-lg shadow-orange-500/25 disabled:opacity-50"
                            >
                                {isLoading ? (
                                    <Loader2 className="w-5 h-5 animate-spin" />
                                ) : (
                                    <>
                                        Send Reset Link
                                        <ArrowRight className="w-4 h-4" />
                                    </>
                                )}
                            </button>

                            <p className="text-center text-sm">
                                <button
                                    type="button"
                                    onClick={() => { resetForm(); setMode('login'); }}
                                    className="text-orange-400 hover:text-orange-300 font-medium"
                                >
                                    Back to sign in
                                </button>
                            </p>
                        </form>
                    )}
                </div>
            </div>
        </div>
    );
}
