import { useOrganization } from '../lib/organization-context';
import { useAuth } from '../lib/auth';
import { Clock, Users, LogOut, RefreshCw } from 'lucide-react';

/**
 * PendingApproval Component
 * Shown when a user has no approved organizations but has pending memberships.
 * They're waiting for a coach to approve their request.
 */
export default function PendingApproval() {
    const { pendingOrganizations, refreshOrganizations, isLoading } = useOrganization();
    const { signOut } = useAuth();

    const handleRefresh = async () => {
        await refreshOrganizations();
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center p-4">
            <div className="max-w-md w-full text-center">
                {/* Icon */}
                <div className="inline-flex items-center justify-center w-20 h-20 bg-gradient-to-br from-amber-500/20 to-orange-500/20 rounded-3xl mb-6">
                    <Clock className="w-10 h-10 text-amber-400" />
                </div>

                {/* Title */}
                <h1 className="text-2xl font-bold text-white mb-2">Awaiting Approval</h1>
                <p className="text-slate-400 mb-6">
                    Your request to join has been submitted. A coach will review and approve your access soon.
                </p>

                {/* Pending Organizations List */}
                <div className="bg-slate-800/50 backdrop-blur-xl border border-slate-700 rounded-2xl p-6 mb-6">
                    <h2 className="text-sm font-medium text-slate-400 mb-3 flex items-center justify-center gap-2">
                        <Users className="w-4 h-4" />
                        Pending Memberships
                    </h2>

                    <div className="space-y-3">
                        {pendingOrganizations.map(po => (
                            <div
                                key={po.organization.id}
                                className="bg-slate-700/50 rounded-xl p-4 text-left"
                            >
                                <p className="text-white font-medium">
                                    {po.organization.name}
                                </p>
                                {po.organization.team_number && (
                                    <p className="text-slate-400 text-sm">
                                        Team #{po.organization.team_number}
                                    </p>
                                )}
                                <p className="text-amber-400 text-xs mt-1 flex items-center gap-1">
                                    <Clock className="w-3 h-3" />
                                    Requested {new Date(po.membership.created_at).toLocaleDateString()}
                                </p>
                            </div>
                        ))}
                    </div>
                </div>

                {/* Actions */}
                <div className="space-y-3">
                    <button
                        onClick={handleRefresh}
                        disabled={isLoading}
                        className="w-full flex items-center justify-center gap-2 bg-slate-700 text-white font-medium py-3 px-4 rounded-xl hover:bg-slate-600 transition-colors disabled:opacity-50"
                    >
                        <RefreshCw className={`w-4 h-4 ${isLoading ? 'animate-spin' : ''}`} />
                        Check Status
                    </button>

                    <button
                        onClick={signOut}
                        className="w-full flex items-center justify-center gap-2 text-slate-400 hover:text-white py-2 transition-colors"
                    >
                        <LogOut className="w-4 h-4" />
                        Sign Out
                    </button>
                </div>

                {/* Help Text */}
                <p className="text-slate-500 text-sm mt-6">
                    If you haven't received approval within 24 hours, please contact your team coach directly.
                </p>
            </div>
        </div>
    );
}
