import React, { useState } from 'react';
import { useOrganization } from '../lib/organization-context';
import { Loader2, ArrowRight, Plus, KeyRound } from 'lucide-react';

type OnboardingStep = 'choice' | 'create-org' | 'join-org';

interface OrganizationOnboardingProps {
    onComplete?: () => void;
}

export default function OrganizationOnboarding({ onComplete }: OrganizationOnboardingProps) {
    const { createOrganization, joinOrganization, pendingOrganizations } = useOrganization();

    const [step, setStep] = useState<OnboardingStep>('choice');
    const [isLoading, setIsLoading] = useState(false);
    const [error, setError] = useState<string | null>(null);

    // Create org form
    const [orgName, setOrgName] = useState('');
    const [teamNumber, setTeamNumber] = useState('');

    // Join org form
    const [inviteCode, setInviteCode] = useState('');

    const handleCreateOrg = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsLoading(true);
        setError(null);

        const { error } = await createOrganization(orgName, teamNumber || undefined);

        setIsLoading(false);
        if (error) {
            setError(error.message);
        } else {
            onComplete?.();
        }
    };

    const handleJoinOrg = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsLoading(true);
        setError(null);

        const { error } = await joinOrganization(inviteCode);

        setIsLoading(false);
        if (error) {
            setError(error.message);
        } else {
            // Joining puts user in pending state, show message
            setStep('choice');
        }
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center p-4">
            <div className="max-w-lg w-full">
                {/* Header - matches Login.tsx styling */}
                <div className="text-center mb-8">
                    <div className="inline-flex items-center justify-center w-20 h-20 bg-gradient-to-br from-slate-800 to-slate-900 rounded-3xl shadow-xl border border-slate-700/50 mb-4 p-4">
                        <img
                            src={`${import.meta.env.BASE_URL}falcon_logo.png`}
                            className="w-full h-full object-contain"
                            alt="FalconForge Logo"
                        />
                    </div>
                    <h1 className="text-3xl font-black italic tracking-tighter mb-2">
                        <span className="bg-gradient-to-r from-orange-500 to-amber-500 bg-clip-text text-transparent">FALCON</span>
                        <span className="text-white">FORGE</span>
                    </h1>
                    <p className="text-slate-400">
                        {step === 'choice' && "Let's get you set up with your team."}
                        {step === 'create-org' && "Create your team's workspace."}
                        {step === 'join-org' && "Enter your team's invite code."}
                    </p>
                </div>

                {/* Pending Organizations Banner */}
                {pendingOrganizations.length > 0 && step === 'choice' && (
                    <div className="bg-amber-500/10 border border-amber-500/30 rounded-xl p-4 mb-6">
                        <h3 className="text-amber-300 font-medium mb-1">Pending Approval</h3>
                        <p className="text-amber-300/80 text-sm">
                            You've requested to join {pendingOrganizations.length} team{pendingOrganizations.length > 1 ? 's' : ''}.
                            A coach will need to approve your request before you can access team data.
                        </p>
                        <ul className="mt-2 space-y-1">
                            {pendingOrganizations.map(po => (
                                <li key={po.organization.id} className="text-amber-200 text-sm">
                                     {po.organization.name}
                                    {po.organization.team_number && ` (Team ${po.organization.team_number})`}
                                </li>
                            ))}
                        </ul>
                    </div>
                )}

                {/* Main Card */}
                <div className="bg-slate-800/50 backdrop-blur-xl border border-slate-700 rounded-2xl p-6 shadow-xl">
                    {error && (
                        <div className="bg-red-500/10 border border-red-500/20 rounded-xl p-3 mb-4">
                            <p className="text-red-400 text-sm">{error}</p>
                        </div>
                    )}

                    {/* Step: Choice */}
                    {step === 'choice' && (
                        <div className="space-y-4">
                            {/* Create Team Option */}
                            <button
                                onClick={() => { setError(null); setStep('create-org'); }}
                                className="w-full p-4 bg-slate-700/50 border border-slate-600 rounded-xl hover:border-orange-500/50 hover:bg-slate-700 transition-all text-left group"
                            >
                                <div className="flex items-center gap-3 mb-2">
                                    <div className="w-10 h-10 rounded-xl bg-orange-500/20 flex items-center justify-center">
                                        <Plus className="w-5 h-5 text-orange-400" />
                                    </div>
                                    <div>
                                        <h3 className="text-white font-semibold group-hover:text-orange-400 transition-colors">
                                            Create a Team
                                        </h3>
                                        <p className="text-slate-400 text-xs">I'm a coach setting up a new workspace</p>
                                    </div>
                                </div>
                                <p className="text-slate-500 text-xs pl-[52px]">
                                    Create your team, invite members, and manage billing
                                </p>
                            </button>

                            {/* Join Team Option */}
                            <button
                                onClick={() => { setError(null); setStep('join-org'); }}
                                className="w-full p-4 bg-slate-700/50 border border-slate-600 rounded-xl hover:border-blue-500/50 hover:bg-slate-700 transition-all text-left group"
                            >
                                <div className="flex items-center gap-3 mb-2">
                                    <div className="w-10 h-10 rounded-xl bg-blue-500/20 flex items-center justify-center">
                                        <KeyRound className="w-5 h-5 text-blue-400" />
                                    </div>
                                    <div>
                                        <h3 className="text-white font-semibold group-hover:text-blue-400 transition-colors">
                                            Join a Team
                                        </h3>
                                        <p className="text-slate-400 text-xs">I have an invite code from my coach</p>
                                    </div>
                                </div>
                                <p className="text-slate-500 text-xs pl-[52px]">
                                    Enter your invite code to request access
                                </p>
                            </button>
                        </div>
                    )}

                    {/* Step: Create Organization */}
                    {step === 'create-org' && (
                        <form onSubmit={handleCreateOrg} className="space-y-4">
                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">
                                    Team Name <span className="text-red-400">*</span>
                                </label>
                                <input
                                    type="text"
                                    value={orgName}
                                    onChange={(e) => setOrgName(e.target.value)}
                                    className="w-full px-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                                    placeholder="e.g., Falcon Robotics"
                                    required
                                />
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">
                                    Team Number <span className="text-slate-500">(optional)</span>
                                </label>
                                <input
                                    type="text"
                                    value={teamNumber}
                                    onChange={(e) => setTeamNumber(e.target.value)}
                                    className="w-full px-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-orange-500 focus:border-transparent"
                                    placeholder="e.g., 12345"
                                />
                                <p className="text-slate-500 text-xs mt-1">
                                    Your FTC team number (if applicable)
                                </p>
                            </div>

                            <div className="flex gap-3 pt-2">
                                <button
                                    type="button"
                                    onClick={() => setStep('choice')}
                                    className="flex-1 px-4 py-3 bg-slate-700 text-slate-300 font-medium rounded-xl hover:bg-slate-600 transition-colors"
                                >
                                    Back
                                </button>
                                <button
                                    type="submit"
                                    disabled={isLoading || !orgName.trim()}
                                    className="flex-1 flex items-center justify-center gap-2 bg-gradient-to-r from-orange-500 to-orange-600 text-white font-semibold py-3 px-4 rounded-xl hover:from-orange-600 hover:to-orange-700 transition-all shadow-lg shadow-orange-500/25 disabled:opacity-50"
                                >
                                    {isLoading ? (
                                        <Loader2 className="w-5 h-5 animate-spin" />
                                    ) : (
                                        <>
                                            Create Team
                                            <ArrowRight className="w-4 h-4" />
                                        </>
                                    )}
                                </button>
                            </div>
                        </form>
                    )}

                    {/* Step: Join Organization */}
                    {step === 'join-org' && (
                        <form onSubmit={handleJoinOrg} className="space-y-4">
                            <div>
                                <label className="block text-sm font-medium text-slate-300 mb-1.5">
                                    Invite Code <span className="text-red-400">*</span>
                                </label>
                                <input
                                    type="text"
                                    value={inviteCode}
                                    onChange={(e) => setInviteCode(e.target.value.toUpperCase())}
                                    className="w-full px-4 py-3 bg-slate-700/50 border border-slate-600 rounded-xl text-white placeholder-slate-400 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-center text-lg uppercase tracking-widest"
                                    placeholder="ABC123"
                                    required
                                />
                                <p className="text-slate-500 text-xs mt-1 text-center">
                                    Get this code from your team coach
                                </p>
                            </div>

                            <div className="flex gap-3 pt-2">
                                <button
                                    type="button"
                                    onClick={() => setStep('choice')}
                                    className="flex-1 px-4 py-3 bg-slate-700 text-slate-300 font-medium rounded-xl hover:bg-slate-600 transition-colors"
                                >
                                    Back
                                </button>
                                <button
                                    type="submit"
                                    disabled={isLoading || !inviteCode.trim()}
                                    className="flex-1 flex items-center justify-center gap-2 bg-gradient-to-r from-blue-500 to-blue-600 text-white font-semibold py-3 px-4 rounded-xl hover:from-blue-600 hover:to-blue-700 transition-all shadow-lg shadow-blue-500/25 disabled:opacity-50"
                                >
                                    {isLoading ? (
                                        <Loader2 className="w-5 h-5 animate-spin" />
                                    ) : (
                                        <>
                                            Request to Join
                                            <ArrowRight className="w-4 h-4" />
                                        </>
                                    )}
                                </button>
                            </div>
                        </form>
                    )}
                </div>

                {/* Help Text */}
                <p className="text-center text-slate-500 text-sm mt-4">
                    Need help? Contact your team coach or{' '}
                    <a href="/support" className="text-orange-400 hover:underline">
                        our support team
                    </a>
                </p>
            </div>
        </div>
    );
}
