/**
 * Sync Engine for Organization Data
 * 
 * Handles bidirectional sync between local store and Supabase:
 * - Fetches data from Supabase when online
 * - Pushes local changes to Supabase
 * - Queues changes when offline for later sync
 * - Server wins on conflicts (simplest strategy)
 */

import { supabase, isSupabaseConfigured } from './supabase';

// Types for sync operations
export interface SyncableData {
    seasons: Season[];
    tasks: Task[];
    scoutingReports: ScoutingReport[];
    checklistItems: ChecklistItem[];
    matchPlans: MatchPlan[];
    portfolioHistory: PortfolioEntry[];
    subTeams: SubTeam[];
    rosterMembers: RosterMember[];
}

export interface Season {
    id: string;
    organization_id: string;
    name: string;
    field_image_url: string;
    created_at: string;
    updated_at: string;
}

export interface Task {
    id: string;
    organization_id: string;
    season_id: string | null;
    title: string;
    description: string;
    status: string;
    type: string;
    assigned_to: string;
    department: string;
    tags: string[];
    checklist: { id: string; text: string; completed: boolean }[];
    timeline: { id: string; type: string; authorId: string; content: string; timestamp: number }[];
    due_date: string | null;
    created_at: string;
    updated_at: string;
}

export interface ScoutingReport {
    id: string;
    organization_id: string;
    season_id: string | null;
    team_number: string;
    match_number: number;
    has_autonomous: boolean;
    auto_score: number;
    intake_type: string;
    auto_aim: boolean;
    far_shooting: boolean;
    shots_taken: number;
    shots_missed: number;
    parking: string;
    rating: number;
    end_game_notes: string;
    created_at: string;
}

export interface ChecklistItem {
    id: string;
    organization_id: string;
    season_id: string | null;
    text: string;
    checked: boolean;
    assigned_to: string;
    sort_order: number;
}

export interface MatchPlan {
    id: string;
    organization_id: string;
    season_id: string | null;
    title: string;
    drawing_data: any;
    notes: string;
    alliance_team: string;
    partner_autonomous: boolean;
    partner_park: boolean;
    updated_at: string;
}

export interface PortfolioEntry {
    id: string;
    organization_id: string;
    season_id: string | null;
    content: string;
    task_count: number;
    created_at: string;
}

export interface SubTeam {
    id: string;
    organization_id: string;
    season_id: string | null;
    name: string;
    member_ids: string[];
}

export interface RosterMember {
    id: string;
    organization_id: string;
    season_id: string | null;
    first_name: string;
    last_name_initial: string;
    user_id: string | null;
}

// Offline sync queue (stored in localStorage)
interface SyncQueueItem {
    id: string;
    table: string;
    action: 'create' | 'update' | 'delete';
    data: any;
    timestamp: number;
}

const SYNC_QUEUE_KEY = 'ftc-sync-queue';

/**
 * Get the offline sync queue
 */
function getSyncQueue(): SyncQueueItem[] {
    try {
        const queue = localStorage.getItem(SYNC_QUEUE_KEY);
        return queue ? JSON.parse(queue) : [];
    } catch {
        return [];
    }
}

/**
 * Save the sync queue
 */
function setSyncQueue(queue: SyncQueueItem[]): void {
    localStorage.setItem(SYNC_QUEUE_KEY, JSON.stringify(queue));
}

/**
 * Add an item to the sync queue (for offline changes)
 */
export function queueSyncItem(table: string, action: 'create' | 'update' | 'delete', data: any): void {
    const queue = getSyncQueue();

    // Remove any existing queue item for this record (we only need the latest)
    const filteredQueue = queue.filter(item => !(item.table === table && item.data?.id === data?.id));

    // Don't queue deletes for items that were never synced
    if (action === 'delete') {
        const wasNeverSynced = queue.some(item =>
            item.table === table &&
            item.data?.id === data?.id &&
            item.action === 'create'
        );
        if (wasNeverSynced) {
            setSyncQueue(filteredQueue);
            return;
        }
    }

    filteredQueue.push({
        id: crypto.randomUUID(),
        table,
        action,
        data,
        timestamp: Date.now(),
    });

    setSyncQueue(filteredQueue);
}

/**
 * Check if we're online
 */
export function isOnline(): boolean {
    return navigator.onLine;
}

/**
 * Fetch all data for an organization from Supabase
 */
export async function fetchOrgData(organizationId: string): Promise<SyncableData | null> {
    if (!isSupabaseConfigured() || !supabase) {
        console.log('Supabase not configured, using local data only');
        return null;
    }

    if (!isOnline()) {
        console.log('Offline, using local data');
        return null;
    }

    try {
        const client = supabase as any;

        // Fetch all data in parallel
        const [
            seasonsResult,
            tasksResult,
            scoutingResult,
            checklistResult,
            matchPlansResult,
            portfolioResult,
            subTeamsResult,
            rosterResult,
        ] = await Promise.all([
            client.from('seasons').select('*').eq('organization_id', organizationId),
            client.from('tasks').select('*').eq('organization_id', organizationId),
            client.from('scouting_reports').select('*').eq('organization_id', organizationId),
            client.from('checklist_items').select('*').eq('organization_id', organizationId),
            client.from('match_plans').select('*').eq('organization_id', organizationId),
            client.from('portfolio_history').select('*').eq('organization_id', organizationId),
            client.from('sub_teams').select('*').eq('organization_id', organizationId),
            client.from('roster_members').select('*').eq('organization_id', organizationId),
        ]);

        return {
            seasons: seasonsResult.data || [],
            tasks: tasksResult.data || [],
            scoutingReports: scoutingResult.data || [],
            checklistItems: checklistResult.data || [],
            matchPlans: matchPlansResult.data || [],
            portfolioHistory: portfolioResult.data || [],
            subTeams: subTeamsResult.data || [],
            rosterMembers: rosterResult.data || [],
        };
    } catch (error) {
        console.error('Error fetching org data:', error);
        return null;
    }
}

/**
 * Push a single item to Supabase (upsert)
 */
export async function pushToSupabase(table: string, data: any): Promise<boolean> {
    if (!isSupabaseConfigured() || !supabase || !isOnline()) {
        return false;
    }

    try {
        const client = supabase as any;
        const { error } = await client
            .from(table)
            .upsert(data, { onConflict: 'id' });

        if (error) {
            console.error(`Error upserting to ${table}:`, error);
            return false;
        }
        return true;
    } catch (error) {
        console.error(`Error pushing to ${table}:`, error);
        return false;
    }
}

/**
 * Delete an item from Supabase
 */
export async function deleteFromSupabase(table: string, id: string): Promise<boolean> {
    if (!isSupabaseConfigured() || !supabase || !isOnline()) {
        return false;
    }

    try {
        const client = supabase as any;
        const { error } = await client
            .from(table)
            .delete()
            .eq('id', id);

        if (error) {
            console.error(`Error deleting from ${table}:`, error);
            return false;
        }
        return true;
    } catch (error) {
        console.error(`Error deleting from ${table}:`, error);
        return false;
    }
}

/**
 * Process the offline sync queue
 */
export async function processSyncQueue(): Promise<number> {
    if (!isOnline()) {
        return 0;
    }

    const queue = getSyncQueue();
    if (queue.length === 0) {
        return 0;
    }

    let processed = 0;
    const remaining: SyncQueueItem[] = [];

    for (const item of queue) {
        let success = false;

        if (item.action === 'delete') {
            success = await deleteFromSupabase(item.table, item.data.id);
        } else {
            success = await pushToSupabase(item.table, item.data);
        }

        if (success) {
            processed++;
        } else {
            remaining.push(item);
        }
    }

    setSyncQueue(remaining);
    return processed;
}

/**
 * Initialize default data for a new organization
 */
export async function initializeOrgDefaults(organizationId: string): Promise<void> {
    if (!isSupabaseConfigured() || !supabase || !isOnline()) {
        return;
    }

    try {
        const client = supabase as any;

        // Check if org already has a season
        const { data: existingSeasons } = await client
            .from('seasons')
            .select('id')
            .eq('organization_id', organizationId)
            .limit(1);

        if (existingSeasons && existingSeasons.length > 0) {
            return; // Already initialized
        }

        // Create default season
        const seasonId = crypto.randomUUID();
        await client.from('seasons').insert({
            id: seasonId,
            organization_id: organizationId,
            name: '2025-2026 Season',
            field_image_url: '',
        });

        // Create default sub-teams
        const defaultSubTeams = ['Build', 'Programming', 'Drive', 'Scouting', 'Outreach'];
        await client.from('sub_teams').insert(
            defaultSubTeams.map(name => ({
                id: crypto.randomUUID(),
                organization_id: organizationId,
                season_id: seasonId,
                name,
                member_ids: [],
            }))
        );

        // Create default checklist items
        const defaultChecklist = [
            'Turn off robot',
            'Swap main battery',
            'Charge old battery',
            'Charge Driver Hub',
            'Tighten chassis screws',
            'Check wiring connections',
            'Clean wheels',
            'Reset servo positions',
        ];
        await client.from('checklist_items').insert(
            defaultChecklist.map((text, i) => ({
                id: crypto.randomUUID(),
                organization_id: organizationId,
                season_id: seasonId,
                text,
                checked: false,
                assigned_to: '',
                sort_order: i,
            }))
        );

        console.log('Initialized default data for organization:', organizationId);
    } catch (error) {
        console.error('Error initializing org defaults:', error);
    }
}

/**
 * Listen for online/offline events to trigger sync
 */
export function setupSyncListeners(onSync: () => void): () => void {
    const handleOnline = async () => {
        console.log('Back online, processing sync queue...');
        const processed = await processSyncQueue();
        if (processed > 0) {
            console.log(`Synced ${processed} items`);
            onSync();
        }
    };

    window.addEventListener('online', handleOnline);

    return () => {
        window.removeEventListener('online', handleOnline);
    };
}
