import { ArrowLeft } from 'lucide-react';
import { Link } from 'react-router-dom';

export default function Privacy() {
    return (
        <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 py-12 px-4">
            <div className="max-w-3xl mx-auto">
                <Link
                    to="/login"
                    className="inline-flex items-center gap-2 text-slate-400 hover:text-white mb-8 transition-colors"
                >
                    <ArrowLeft className="w-4 h-4" />
                    Back to Login
                </Link>

                <div className="bg-slate-800 rounded-xl border border-slate-700 p-8">
                    <h1 className="text-3xl font-bold text-white mb-6">Privacy Policy</h1>

                    <div className="prose prose-invert prose-slate max-w-none">
                        <p className="text-slate-300">
                            Last updated: {new Date().toLocaleDateString()}
                        </p>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">1. Information We Collect</h2>
                        <p className="text-slate-300">
                            We collect information you provide directly, including:
                        </p>
                        <ul className="text-slate-300 list-disc pl-6 space-y-2">
                            <li>Account information (name, email, role)</li>
                            <li>Team and organization data</li>
                            <li>Task and project information</li>
                            <li>Scouting reports and match data</li>
                        </ul>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">2. How We Use Information</h2>
                        <p className="text-slate-300">
                            We use collected information to:
                        </p>
                        <ul className="text-slate-300 list-disc pl-6 space-y-2">
                            <li>Provide and maintain the Service</li>
                            <li>Process transactions and send related notices</li>
                            <li>Send technical alerts and support messages</li>
                            <li>Improve and develop new features</li>
                        </ul>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">3. Data Sharing</h2>
                        <p className="text-slate-300">
                            We do not sell your personal information. We may share data with:
                        </p>
                        <ul className="text-slate-300 list-disc pl-6 space-y-2">
                            <li>Service providers (hosting, analytics)</li>
                            <li>Legal authorities when required by law</li>
                            <li>Team members within your organization</li>
                        </ul>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">4. Data Security</h2>
                        <p className="text-slate-300">
                            We implement industry-standard security measures including encryption, secure hosting, and access controls.
                        </p>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">5. Children's Privacy</h2>
                        <p className="text-slate-300">
                            Users under 18 require parental/guardian consent. See our <Link to="/parental-consent" className="text-orange-400 hover:text-orange-300">Parental Consent Policy</Link>.
                        </p>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">6. Your Rights</h2>
                        <p className="text-slate-300">
                            You may request access to, correction of, or deletion of your data by contacting us.
                        </p>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">7. Contact</h2>
                        <p className="text-slate-300">
                            For privacy questions, contact us at privacy@falconforge.app
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
}
