import { useState, useEffect } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useAuth } from '../lib/auth';
import { useOrganization } from '../lib/organization-context';
import { supabase } from '../lib/supabase';
import { Loader2, Users, AlertTriangle, Check, LogIn } from 'lucide-react';
import type { OrganizationInvite, Organization } from '../lib/database.types';

interface InviteDetails {
    invite: OrganizationInvite;
    organization: Organization;
}

export default function JoinTeam() {
    const { code } = useParams<{ code: string }>();
    const navigate = useNavigate();
    const { user, isLoading: authLoading, isConfigured } = useAuth();
    const { joinOrganization, refreshOrganizations } = useOrganization();

    const [inviteDetails, setInviteDetails] = useState<InviteDetails | null>(null);
    const [isLoading, setIsLoading] = useState(true);
    const [isJoining, setIsJoining] = useState(false);
    const [error, setError] = useState<string | null>(null);
    const [success, setSuccess] = useState(false);

    // Fetch invite details
    useEffect(() => {
        if (!code || !supabase) {
            setError('Invalid invite link');
            setIsLoading(false);
            return;
        }

        fetchInviteDetails();
    }, [code]);

    const fetchInviteDetails = async () => {
        if (!code || !supabase) return;

        setIsLoading(true);
        setError(null);

        try {
            const client = supabase as any;

            // Fetch invite with organization data
            const { data: invite, error: inviteError } = await client
                .from('organization_invites')
                .select(`
                    *,
                    organization:organizations(*)
                `)
                .eq('invite_code', code.toUpperCase())
                .eq('active', true)
                .single();

            if (inviteError || !invite) {
                setError('This invite link is invalid or has expired.');
                setIsLoading(false);
                return;
            }

            // Check expiration
            if (invite.expires_at && new Date(invite.expires_at) < new Date()) {
                setError('This invite link has expired.');
                setIsLoading(false);
                return;
            }

            // Check max uses
            if (invite.max_uses !== null && invite.use_count >= invite.max_uses) {
                setError('This invite link has reached its maximum uses.');
                setIsLoading(false);
                return;
            }

            setInviteDetails({
                invite: invite,
                organization: invite.organization,
            });
        } catch (err: any) {
            console.error('Error fetching invite:', err);
            setError('Failed to load invite details.');
        } finally {
            setIsLoading(false);
        }
    };

    const handleJoin = async () => {
        if (!code) return;

        setIsJoining(true);
        setError(null);

        const { success: joined, error: joinError } = await joinOrganization(code);

        if (joinError) {
            setError(joinError.message);
            setIsJoining(false);
            return;
        }

        if (joined) {
            setSuccess(true);
            await refreshOrganizations();

            // Redirect after a short delay
            setTimeout(() => {
                navigate('/');
            }, 2000);
        }

        setIsJoining(false);
    };

    // Loading state
    if (isLoading || authLoading) {
        return (
            <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center p-4">
                <div className="text-center">
                    <Loader2 className="w-12 h-12 animate-spin text-orange-500 mx-auto mb-4" />
                    <p className="text-slate-400">Loading invite...</p>
                </div>
            </div>
        );
    }

    // Error state
    if (error && !inviteDetails) {
        return (
            <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center p-4">
                <div className="max-w-md w-full text-center">
                    <div className="inline-flex items-center justify-center w-16 h-16 bg-red-500/20 rounded-2xl mb-6">
                        <AlertTriangle className="w-8 h-8 text-red-400" />
                    </div>
                    <h1 className="text-2xl font-bold text-white mb-2">Invalid Invite</h1>
                    <p className="text-slate-400 mb-6">{error}</p>
                    <button
                        onClick={() => navigate('/login')}
                        className="px-6 py-3 bg-slate-700 hover:bg-slate-600 text-white rounded-xl transition-colors"
                    >
                        Go to Login
                    </button>
                </div>
            </div>
        );
    }

    // Success state
    if (success) {
        return (
            <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center p-4">
                <div className="max-w-md w-full text-center">
                    <div className="inline-flex items-center justify-center w-16 h-16 bg-green-500/20 rounded-2xl mb-6">
                        <Check className="w-8 h-8 text-green-400" />
                    </div>
                    <h1 className="text-2xl font-bold text-white mb-2">Request Sent!</h1>
                    <p className="text-slate-400 mb-4">
                        Your request to join <strong className="text-white">{inviteDetails?.organization.name}</strong> has been submitted.
                    </p>
                    <p className="text-slate-500 text-sm">
                        A coach will review and approve your request. Redirecting...
                    </p>
                </div>
            </div>
        );
    }

    // Not logged in - prompt to login/signup
    if (isConfigured && !user) {
        return (
            <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center p-4">
                <div className="max-w-md w-full">
                    {/* Invite Card */}
                    <div className="bg-slate-800/50 backdrop-blur-xl border border-slate-700 rounded-2xl p-6 mb-6">
                        <div className="text-center mb-6">
                            <div className="inline-flex items-center justify-center w-16 h-16 bg-orange-500/20 rounded-2xl mb-4">
                                <Users className="w-8 h-8 text-orange-400" />
                            </div>
                            <h1 className="text-2xl font-bold text-white mb-2">
                                You're Invited!
                            </h1>
                            <p className="text-slate-400">
                                Join <strong className="text-white">{inviteDetails?.organization.name}</strong>
                                {inviteDetails?.organization.team_number && (
                                    <span className="text-slate-500"> (Team #{inviteDetails.organization.team_number})</span>
                                )}
                            </p>
                        </div>

                        <div className="bg-slate-700/50 rounded-xl p-4 mb-6">
                            <p className="text-sm text-slate-400 text-center">
                                Sign in or create an account to join this team
                            </p>
                        </div>

                        <button
                            onClick={() => navigate(`/login?invite=${code}`)}
                            className="w-full flex items-center justify-center gap-2 bg-gradient-to-r from-orange-500 to-orange-600 text-white font-semibold py-3 px-4 rounded-xl hover:from-orange-600 hover:to-orange-700 transition-all shadow-lg shadow-orange-500/25"
                        >
                            <LogIn className="w-5 h-5" />
                            Sign In or Sign Up
                        </button>
                    </div>
                </div>
            </div>
        );
    }

    // Logged in - show join confirmation
    return (
        <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center p-4">
            <div className="max-w-md w-full">
                {/* Invite Card */}
                <div className="bg-slate-800/50 backdrop-blur-xl border border-slate-700 rounded-2xl p-6">
                    <div className="text-center mb-6">
                        <div className="inline-flex items-center justify-center w-16 h-16 bg-orange-500/20 rounded-2xl mb-4">
                            <Users className="w-8 h-8 text-orange-400" />
                        </div>
                        <h1 className="text-2xl font-bold text-white mb-2">
                            Join Team
                        </h1>
                        <p className="text-slate-400">
                            You've been invited to join
                        </p>
                    </div>

                    {/* Organization Info */}
                    <div className="bg-slate-700/50 rounded-xl p-4 mb-6">
                        <h2 className="text-xl font-bold text-white text-center">
                            {inviteDetails?.organization.name}
                        </h2>
                        {inviteDetails?.organization.team_number && (
                            <p className="text-slate-400 text-center mt-1">
                                Team #{inviteDetails.organization.team_number}
                            </p>
                        )}
                    </div>

                    {/* Error Message */}
                    {error && (
                        <div className="bg-red-500/10 border border-red-500/20 rounded-xl p-3 mb-4">
                            <p className="text-red-400 text-sm text-center">{error}</p>
                        </div>
                    )}

                    {/* Join Button */}
                    <button
                        onClick={handleJoin}
                        disabled={isJoining}
                        className="w-full flex items-center justify-center gap-2 bg-gradient-to-r from-orange-500 to-orange-600 text-white font-semibold py-3 px-4 rounded-xl hover:from-orange-600 hover:to-orange-700 transition-all shadow-lg shadow-orange-500/25 disabled:opacity-50"
                    >
                        {isJoining ? (
                            <Loader2 className="w-5 h-5 animate-spin" />
                        ) : (
                            <>
                                <Users className="w-5 h-5" />
                                Request to Join
                            </>
                        )}
                    </button>

                    <p className="text-slate-500 text-xs text-center mt-4">
                        A coach will need to approve your request before you can access team data.
                    </p>
                </div>
            </div>
        </div>
    );
}
