import React, { createContext, useContext, useEffect, useState, useCallback } from 'react';
import { User, Session, AuthError } from '@supabase/supabase-js';
import { supabase, isSupabaseConfigured } from './supabase';
import { loadUserDataFromServer } from './sync';
import type { AccountType, UserUpdate } from './database.types';

/**
 * Signup options for different user types
 */
export interface CoachSignupOptions {
    email: string;
    password: string;
    fullName: string;
    confirmedAge18Plus: boolean;
    acceptedTerms: boolean;
    acknowledgedCoppaResponsibility: boolean;
}

export interface StudentMentorSignupOptions {
    email: string;
    password: string;
    fullName: string;
    confirmedAge13Plus: boolean;
    acknowledgedPrivacyPolicy: boolean;
    acknowledgedCommunityGuidelines: boolean;
    inviteCode?: string; // Optional: can be provided during or after signup
}

interface AuthState {
    user: User | null;
    session: Session | null;
    isLoading: boolean;
    isConfigured: boolean;
}

interface AuthContextType extends AuthState {
    // Standard auth
    signInWithEmail: (email: string, password: string) => Promise<{ error: AuthError | null }>;
    signOut: () => Promise<void>;
    resetPassword: (email: string) => Promise<{ error: AuthError | null }>;
    updateProfile: (fullName: string) => Promise<{ error: AuthError | null }>;

    // Role-based signup
    signUpAsCoach: (options: CoachSignupOptions) => Promise<{ error: AuthError | Error | null }>;
    signUpAsStudentOrMentor: (options: StudentMentorSignupOptions) => Promise<{ error: AuthError | Error | null }>;

    // Legacy signup (for backward compatibility, wraps signUpAsStudentOrMentor)
    signUpWithEmail: (email: string, password: string, fullName: string) => Promise<{ error: AuthError | null }>;

    // OAuth (will need reconfiguration for role selection post-auth)
    signInWithGoogle: () => Promise<{ error: AuthError | null }>;
    signInWithMicrosoft: () => Promise<{ error: AuthError | null }>;

    // Profile updates
    updateComplianceInfo: (updates: {
        age_confirmed_13_plus?: boolean;
        age_confirmed_18_plus?: boolean;
        terms_accepted_at?: string;
        community_guidelines_acknowledged_at?: string;
        privacy_policy_acknowledged_at?: string;
        account_type?: AccountType;
    }) => Promise<{ error: Error | null }>;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: React.ReactNode }) {
    const [state, setState] = useState<AuthState>({
        user: null,
        session: null,
        isLoading: true,
        isConfigured: isSupabaseConfigured(),
    });

    useEffect(() => {
        if (!supabase) {
            setState(prev => ({ ...prev, isLoading: false }));
            return;
        }

        // Safety timeout for initial auth check (especially useful for offline PWA)
        const authTimeout = setTimeout(() => {
            setState(prev => {
                if (prev.isLoading) {
                    console.warn('Auth check timed out, proceeding to app...');
                    return { ...prev, isLoading: false };
                }
                return prev;
            });
        }, 1500);

        // Get initial session
        supabase.auth.getSession().then(({ data: { session } }) => {
            clearTimeout(authTimeout);
            setState(prev => ({
                ...prev,
                session,
                user: session?.user ?? null,
                isLoading: false,
            }));
        }).catch(err => {
            console.error('Auth session error:', err);
            clearTimeout(authTimeout);
            setState(prev => ({ ...prev, isLoading: false }));
        });

        // Listen for auth changes
        const { data: { subscription } } = supabase.auth.onAuthStateChange(
            async (event, session) => {
                setState(prev => ({
                    ...prev,
                    session,
                    user: session?.user ?? null,
                    isLoading: false,
                }));

                // Handle user profile creation on first sign up
                if (event === 'SIGNED_IN' && session?.user) {
                    await ensureUserProfile(session.user);
                    // Load user data from server after sign in
                    await loadUserDataFromServer();
                }
            }
        );

        return () => {
            subscription.unsubscribe();
        };
    }, []);

    const ensureUserProfile = async (user: User) => {
        if (!supabase) return;

        // Check if user profile exists
        const { data: existingUser } = await supabase
            .from('users')
            .select('id')
            .eq('id', user.id)
            .single();

        // Create profile if it doesn't exist
        if (!existingUser) {
            await supabase.from('users').insert({
                id: user.id,
                email: user.email!,
                full_name: user.user_metadata.full_name || user.user_metadata.name || null,
                avatar_url: user.user_metadata.avatar_url || null,
                // Compliance fields will be set via separate update based on signup type
            } as any);
        }
    };

    const signInWithEmail = useCallback(async (email: string, password: string) => {
        if (!supabase) return { error: { message: 'Supabase not configured' } as AuthError };

        const { error } = await supabase.auth.signInWithPassword({ email, password });
        return { error };
    }, []);

    /**
     * Coach signup - requires age 18+, terms acceptance, COPPA acknowledgment
     */
    const signUpAsCoach = useCallback(async (options: CoachSignupOptions) => {
        if (!supabase) return { error: new Error('Supabase not configured') };

        // Validate requirements
        if (!options.confirmedAge18Plus) {
            return { error: new Error('You must be 18 years or older to create a coach account') };
        }
        if (!options.acceptedTerms) {
            return { error: new Error('You must accept the Terms and Conditions') };
        }
        if (!options.acknowledgedCoppaResponsibility) {
            return { error: new Error('You must acknowledge responsibility for COPPA compliance') };
        }

        // Create the auth user
        const { data, error } = await supabase.auth.signUp({
            email: options.email,
            password: options.password,
            options: {
                data: {
                    full_name: options.fullName,
                    account_type: 'coach',
                },
            },
        });

        if (error) return { error };

        // Update the user profile with compliance info
        if (data.user) {
            const now = new Date().toISOString();
            await supabase.from('users').upsert({
                id: data.user.id,
                email: options.email,
                full_name: options.fullName,
                account_type: 'coach',
                age_confirmed_18_plus: true,
                age_confirmed_13_plus: true, // If 18+, also 13+
                terms_accepted_at: now,
                community_guidelines_acknowledged_at: now,
                privacy_policy_acknowledged_at: now,
            } as any);
        }

        return { error: null };
    }, []);

    /**
     * Student/Mentor signup - invite-only, requires age 13+, guidelines and privacy acknowledgment
     */
    const signUpAsStudentOrMentor = useCallback(async (options: StudentMentorSignupOptions) => {
        if (!supabase) return { error: new Error('Supabase not configured') };

        // Validate age requirement
        if (!options.confirmedAge13Plus) {
            return { error: new Error('You must be at least 13 years old to create an account. Please ask a parent or guardian to contact your team coach.') };
        }
        if (!options.acknowledgedPrivacyPolicy) {
            return { error: new Error('You must acknowledge the Privacy Policy') };
        }
        if (!options.acknowledgedCommunityGuidelines) {
            return { error: new Error('You must acknowledge the Community Guidelines') };
        }

        // Create the auth user
        const { data, error } = await supabase.auth.signUp({
            email: options.email,
            password: options.password,
            options: {
                data: {
                    full_name: options.fullName,
                    account_type: 'standard',
                    invite_code: options.inviteCode || null,
                },
            },
        });

        if (error) return { error };

        // Update the user profile with compliance info
        if (data.user) {
            const now = new Date().toISOString();
            await supabase.from('users').upsert({
                id: data.user.id,
                email: options.email,
                full_name: options.fullName,
                account_type: 'standard',
                age_confirmed_13_plus: true,
                age_confirmed_18_plus: false, // Unknown, not confirmed
                community_guidelines_acknowledged_at: now,
                privacy_policy_acknowledged_at: now,
                // Note: No terms_accepted_at - students don't accept terms
            } as any);
        }

        return { error: null };
    }, []);

    // Legacy signup for backward compatibility
    const signUpWithEmail = useCallback(async (email: string, password: string, fullName: string) => {
        if (!supabase) return { error: { message: 'Supabase not configured' } as AuthError };

        const { error } = await supabase.auth.signUp({
            email,
            password,
            options: {
                data: {
                    full_name: fullName,
                },
            },
        });
        return { error };
    }, []);

    const signInWithGoogle = useCallback(async () => {
        if (!supabase) return { error: { message: 'Supabase not configured' } as AuthError };

        const { error } = await supabase.auth.signInWithOAuth({
            provider: 'google',
            options: {
                redirectTo: `${window.location.origin}/auth/callback`,
            },
        });
        return { error };
    }, []);

    const signInWithMicrosoft = useCallback(async () => {
        if (!supabase) return { error: { message: 'Supabase not configured' } as AuthError };

        const { error } = await supabase.auth.signInWithOAuth({
            provider: 'azure',
            options: {
                redirectTo: `${window.location.origin}/auth/callback`,
                scopes: 'email profile openid',
            },
        });
        return { error };
    }, []);

    const signOut = useCallback(async () => {
        if (!supabase) return;
        await supabase.auth.signOut();
    }, []);

    const resetPassword = useCallback(async (email: string) => {
        if (!supabase) return { error: { message: 'Supabase not configured' } as AuthError };

        const { error } = await supabase.auth.resetPasswordForEmail(email, {
            redirectTo: `${window.location.origin}/auth/reset-password`,
        });
        return { error };
    }, []);

    const updateProfile = useCallback(async (fullName: string) => {
        if (!supabase) return { error: { message: 'Supabase not configured' } as AuthError };

        const { data, error } = await supabase.auth.updateUser({
            data: { full_name: fullName }
        });

        // Update local state with the new user data
        if (!error && data.user) {
            setState(prev => ({
                ...prev,
                user: data.user,
            }));
        }

        return { error };
    }, []);

    /**
     * Update compliance information in the user profile
     */
    const updateComplianceInfo = useCallback(async (updates: Partial<UserUpdate>) => {
        if (!supabase || !state.user) {
            return { error: new Error('Not authenticated') };
        }

        // eslint-disable-next-line @typescript-eslint/no-explicit-any
        const { error } = await (supabase as any)
            .from('users')
            .update(updates)
            .eq('id', state.user.id);

        if (error) {
            return { error: new Error(error.message) };
        }

        return { error: null };
    }, [state.user]);

    const value: AuthContextType = {
        ...state,
        signInWithEmail,
        signUpWithEmail,
        signUpAsCoach,
        signUpAsStudentOrMentor,
        signInWithGoogle,
        signInWithMicrosoft,
        signOut,
        resetPassword,
        updateProfile,
        updateComplianceInfo,
    };

    return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
}

export function useAuth() {
    const context = useContext(AuthContext);
    if (context === undefined) {
        throw new Error('useAuth must be used within an AuthProvider');
    }
    return context;
}

// Hook to require authentication
export function useRequireAuth() {
    const auth = useAuth();
    return auth;
}
