import { useState, useEffect } from 'react';
import { useAuth } from '../lib/auth';
import { supabase } from '../lib/supabase';
import {
    Shield, Building2, Users, Clock, Search,
    ChevronDown, ChevronRight, AlertTriangle, LogIn, ArrowRightLeft,
    RefreshCw, Loader2, Eye
} from 'lucide-react';

interface Organization {
    id: string;
    name: string;
    team_number: string | null;
    owner_user_id: string;
    created_at: string;
    member_count?: number;
    owner_email?: string;
}

interface UserInfo {
    id: string;
    email: string;
    full_name: string | null;
    is_super_admin: boolean;
    created_at: string;
}

interface AuditLogEntry {
    id: string;
    admin_user_id: string;
    action_type: string;
    target_type: string;
    target_id: string | null;
    details: Record<string, any>;
    created_at: string;
}

export default function SuperAdminDashboard() {
    const { user } = useAuth();
    const [isSuperAdmin, setIsSuperAdmin] = useState<boolean | null>(null);
    const [isLoading, setIsLoading] = useState(true);
    const [activeTab, setActiveTab] = useState<'orgs' | 'users' | 'audit'>('orgs');

    // Data
    const [organizations, setOrganizations] = useState<Organization[]>([]);
    const [users, setUsers] = useState<UserInfo[]>([]);
    const [auditLogs, setAuditLogs] = useState<AuditLogEntry[]>([]);

    // Search
    const [orgSearch, setOrgSearch] = useState('');
    const [userSearch, setUserSearch] = useState('');

    // Selected items
    const [selectedOrg, setSelectedOrg] = useState<string | null>(null);
    const [orgMembers, setOrgMembers] = useState<any[]>([]);

    // Check if user is super admin
    useEffect(() => {
        checkSuperAdmin();
    }, [user]);

    const checkSuperAdmin = async () => {
        if (!supabase || !user) {
            setIsSuperAdmin(false);
            setIsLoading(false);
            return;
        }

        try {
            const client = supabase as any;
            const { data, error } = await client
                .from('users')
                .select('is_super_admin')
                .eq('id', user.id)
                .single();

            if (error) throw error;
            setIsSuperAdmin(data?.is_super_admin || false);

            if (data?.is_super_admin) {
                loadAllData();
            }
        } catch (err) {
            console.error('Error checking super admin:', err);
            setIsSuperAdmin(false);
        } finally {
            setIsLoading(false);
        }
    };

    const loadAllData = async () => {
        await Promise.all([
            loadOrganizations(),
            loadUsers(),
            loadAuditLogs(),
        ]);
    };

    const loadOrganizations = async () => {
        if (!supabase) return;
        try {
            const client = supabase as any;

            // Get organizations
            const { data: orgs, error } = await client
                .from('organizations')
                .select('*')
                .order('created_at', { ascending: false });

            if (error) throw error;

            // Enrich with member counts and owner emails
            const enrichedOrgs = await Promise.all((orgs || []).map(async (org: Organization) => {
                const { count } = await client
                    .from('organization_members')
                    .select('*', { count: 'exact', head: true })
                    .eq('organization_id', org.id)
                    .eq('status', 'approved');

                const { data: owner } = await client
                    .from('users')
                    .select('email')
                    .eq('id', org.owner_user_id)
                    .single();

                return {
                    ...org,
                    member_count: count || 0,
                    owner_email: owner?.email,
                };
            }));

            setOrganizations(enrichedOrgs);
        } catch (err) {
            console.error('Error loading organizations:', err);
        }
    };

    const loadUsers = async () => {
        if (!supabase) return;
        try {
            const client = supabase as any;
            const { data, error } = await client
                .from('users')
                .select('*')
                .order('created_at', { ascending: false });

            if (error) throw error;
            setUsers(data || []);
        } catch (err) {
            console.error('Error loading users:', err);
        }
    };

    const loadAuditLogs = async () => {
        if (!supabase) return;
        try {
            const client = supabase as any;
            const { data, error } = await client
                .from('admin_audit_log')
                .select('*')
                .order('created_at', { ascending: false })
                .limit(100);

            if (error) throw error;
            setAuditLogs(data || []);
        } catch (err) {
            console.error('Error loading audit logs:', err);
        }
    };

    const loadOrgMembers = async (orgId: string) => {
        if (!supabase) return;
        try {
            const client = supabase as any;
            const { data: members } = await client
                .from('organization_members')
                .select('*')
                .eq('organization_id', orgId);

            // Get user details
            const userIds = (members || []).map((m: any) => m.user_id);
            const { data: usersData } = await client
                .from('users')
                .select('id, email, full_name')
                .in('id', userIds);

            const usersMap = Object.fromEntries((usersData || []).map((u: any) => [u.id, u]));

            setOrgMembers((members || []).map((m: any) => ({
                ...m,
                user: usersMap[m.user_id],
            })));
        } catch (err) {
            console.error('Error loading org members:', err);
        }
    };

    const handleImpersonate = (userId: string) => {
        // For now, just show an alert. Full impersonation requires server-side implementation.
        alert(`Impersonation feature would log you in as user ${userId}. This requires server-side implementation with secure session management.`);
    };

    const handleTransferOwnership = async (orgId: string, newOwnerId: string) => {
        if (!supabase) return;
        try {
            const client = supabase as any;
            const { error } = await client.rpc('admin_transfer_ownership', {
                org_id: orgId,
                new_owner_id: newOwnerId,
            });

            if (error) throw error;
            await loadOrganizations();
            alert('Ownership transferred successfully');
        } catch (err: any) {
            console.error('Error transferring ownership:', err);
            alert('Error: ' + err.message);
        }
    };

    // Loading state
    if (isLoading) {
        return (
            <div className="p-6 flex items-center justify-center">
                <Loader2 className="w-8 h-8 animate-spin text-orange-500" />
            </div>
        );
    }

    // Not authorized
    if (!isSuperAdmin) {
        return (
            <div className="p-6 max-w-2xl mx-auto">
                <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-xl p-6 text-center">
                    <AlertTriangle className="w-12 h-12 text-red-500 mx-auto mb-4" />
                    <h2 className="text-xl font-bold text-red-700 dark:text-red-300 mb-2">
                        Access Denied
                    </h2>
                    <p className="text-red-600 dark:text-red-400">
                        You do not have super admin privileges. This page is only accessible to administrators.
                    </p>
                </div>
            </div>
        );
    }

    const filteredOrgs = organizations.filter(org =>
        org.name.toLowerCase().includes(orgSearch.toLowerCase()) ||
        org.team_number?.includes(orgSearch) ||
        org.owner_email?.toLowerCase().includes(orgSearch.toLowerCase())
    );

    const filteredUsers = users.filter(user =>
        user.email.toLowerCase().includes(userSearch.toLowerCase()) ||
        user.full_name?.toLowerCase().includes(userSearch.toLowerCase())
    );

    return (
        <div className="p-3 md:p-6 max-w-7xl mx-auto w-full h-full overflow-y-auto">
            {/* Header */}
            <div className="flex items-center gap-3 mb-6">
                <div className="w-12 h-12 bg-purple-100 dark:bg-purple-900/30 rounded-xl flex items-center justify-center">
                    <Shield className="w-6 h-6 text-purple-600 dark:text-purple-400" />
                </div>
                <div>
                    <h2 className="text-2xl font-bold text-slate-800 dark:text-white">Super Admin Dashboard</h2>
                    <p className="text-sm text-slate-500 dark:text-slate-400">Manage all organizations and users</p>
                </div>
                <button
                    onClick={loadAllData}
                    className="ml-auto p-2 text-slate-500 hover:text-slate-700 dark:text-slate-400 dark:hover:text-white"
                >
                    <RefreshCw className="w-5 h-5" />
                </button>
            </div>

            {/* Tabs */}
            <div className="flex gap-2 mb-6 border-b border-slate-200 dark:border-slate-700">
                <button
                    onClick={() => setActiveTab('orgs')}
                    className={`px-4 py-2 font-medium border-b-2 transition-colors ${activeTab === 'orgs'
                        ? 'border-purple-500 text-purple-600 dark:text-purple-400'
                        : 'border-transparent text-slate-500 hover:text-slate-700'
                        }`}
                >
                    <Building2 className="w-4 h-4 inline mr-2" />
                    Organizations ({organizations.length})
                </button>
                <button
                    onClick={() => setActiveTab('users')}
                    className={`px-4 py-2 font-medium border-b-2 transition-colors ${activeTab === 'users'
                        ? 'border-purple-500 text-purple-600 dark:text-purple-400'
                        : 'border-transparent text-slate-500 hover:text-slate-700'
                        }`}
                >
                    <Users className="w-4 h-4 inline mr-2" />
                    Users ({users.length})
                </button>
                <button
                    onClick={() => setActiveTab('audit')}
                    className={`px-4 py-2 font-medium border-b-2 transition-colors ${activeTab === 'audit'
                        ? 'border-purple-500 text-purple-600 dark:text-purple-400'
                        : 'border-transparent text-slate-500 hover:text-slate-700'
                        }`}
                >
                    <Clock className="w-4 h-4 inline mr-2" />
                    Audit Log
                </button>
            </div>

            {/* Organizations Tab */}
            {activeTab === 'orgs' && (
                <div className="space-y-4">
                    {/* Search */}
                    <div className="relative">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                        <input
                            type="text"
                            value={orgSearch}
                            onChange={(e) => setOrgSearch(e.target.value)}
                            placeholder="Search organizations..."
                            className="w-full pl-10 pr-4 py-2 bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded-lg"
                        />
                    </div>

                    {/* Orgs List */}
                    <div className="bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700 overflow-hidden">
                        {filteredOrgs.map((org) => (
                            <div key={org.id} className="border-b border-slate-100 dark:border-slate-700 last:border-0">
                                <div
                                    className="p-4 flex items-center justify-between cursor-pointer hover:bg-slate-50 dark:hover:bg-slate-700/50"
                                    onClick={() => {
                                        if (selectedOrg === org.id) {
                                            setSelectedOrg(null);
                                        } else {
                                            setSelectedOrg(org.id);
                                            loadOrgMembers(org.id);
                                        }
                                    }}
                                >
                                    <div className="flex items-center gap-3">
                                        {selectedOrg === org.id ? (
                                            <ChevronDown className="w-4 h-4 text-slate-400" />
                                        ) : (
                                            <ChevronRight className="w-4 h-4 text-slate-400" />
                                        )}
                                        <div>
                                            <p className="font-medium text-slate-800 dark:text-white">
                                                {org.name}
                                                {org.team_number && (
                                                    <span className="text-slate-500 ml-2">#{org.team_number}</span>
                                                )}
                                            </p>
                                            <p className="text-sm text-slate-500">Owner: {org.owner_email}</p>
                                        </div>
                                    </div>
                                    <div className="flex items-center gap-4 text-sm text-slate-500">
                                        <span>{org.member_count} members</span>
                                        <span>{new Date(org.created_at).toLocaleDateString()}</span>
                                    </div>
                                </div>

                                {selectedOrg === org.id && (
                                    <div className="bg-slate-50 dark:bg-slate-900/50 p-4 border-t border-slate-100 dark:border-slate-700">
                                        <h4 className="font-medium text-slate-700 dark:text-slate-300 mb-3">Members</h4>
                                        <div className="space-y-2">
                                            {orgMembers.map((member) => (
                                                <div key={member.id} className="flex items-center justify-between bg-white dark:bg-slate-800 p-2 rounded-lg">
                                                    <div>
                                                        <p className="text-sm font-medium text-slate-700 dark:text-slate-300">
                                                            {member.user?.full_name || member.user?.email || member.user_id}
                                                        </p>
                                                        <p className="text-xs text-slate-500">{member.role}  {member.status}</p>
                                                    </div>
                                                    <div className="flex gap-2">
                                                        <button
                                                            onClick={(e) => {
                                                                e.stopPropagation();
                                                                handleImpersonate(member.user_id);
                                                            }}
                                                            className="text-xs px-2 py-1 bg-purple-100 dark:bg-purple-900/30 text-purple-600 dark:text-purple-400 rounded"
                                                        >
                                                            <LogIn className="w-3 h-3 inline mr-1" />
                                                            Impersonate
                                                        </button>
                                                        {member.role !== 'owner' && member.status === 'approved' && (
                                                            <button
                                                                onClick={(e) => {
                                                                    e.stopPropagation();
                                                                    if (confirm(`Transfer ownership to ${member.user?.email}?`)) {
                                                                        handleTransferOwnership(org.id, member.user_id);
                                                                    }
                                                                }}
                                                                className="text-xs px-2 py-1 bg-orange-100 dark:bg-orange-900/30 text-orange-600 dark:text-orange-400 rounded"
                                                            >
                                                                <ArrowRightLeft className="w-3 h-3 inline mr-1" />
                                                                Make Owner
                                                            </button>
                                                        )}
                                                    </div>
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                )}
                            </div>
                        ))}

                        {filteredOrgs.length === 0 && (
                            <div className="p-8 text-center text-slate-500">
                                No organizations found
                            </div>
                        )}
                    </div>
                </div>
            )}

            {/* Users Tab */}
            {activeTab === 'users' && (
                <div className="space-y-4">
                    {/* Search */}
                    <div className="relative">
                        <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-slate-400" />
                        <input
                            type="text"
                            value={userSearch}
                            onChange={(e) => setUserSearch(e.target.value)}
                            placeholder="Search users..."
                            className="w-full pl-10 pr-4 py-2 bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded-lg"
                        />
                    </div>

                    {/* Users List */}
                    <div className="bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700 overflow-hidden">
                        <table className="w-full">
                            <thead className="bg-slate-50 dark:bg-slate-900/50">
                                <tr>
                                    <th className="text-left p-3 text-sm font-medium text-slate-600 dark:text-slate-400">User</th>
                                    <th className="text-left p-3 text-sm font-medium text-slate-600 dark:text-slate-400">Email</th>
                                    <th className="text-left p-3 text-sm font-medium text-slate-600 dark:text-slate-400">Created</th>
                                    <th className="text-left p-3 text-sm font-medium text-slate-600 dark:text-slate-400">Actions</th>
                                </tr>
                            </thead>
                            <tbody>
                                {filteredUsers.map((u) => (
                                    <tr key={u.id} className="border-t border-slate-100 dark:border-slate-700">
                                        <td className="p-3">
                                            <div className="flex items-center gap-2">
                                                <p className="font-medium text-slate-800 dark:text-white">
                                                    {u.full_name || 'No name'}
                                                </p>
                                                {u.is_super_admin && (
                                                    <span className="text-xs px-2 py-0.5 bg-purple-100 dark:bg-purple-900/30 text-purple-600 dark:text-purple-400 rounded">
                                                        Admin
                                                    </span>
                                                )}
                                            </div>
                                        </td>
                                        <td className="p-3 text-sm text-slate-600 dark:text-slate-400">{u.email}</td>
                                        <td className="p-3 text-sm text-slate-500">{new Date(u.created_at).toLocaleDateString()}</td>
                                        <td className="p-3">
                                            <button
                                                onClick={() => handleImpersonate(u.id)}
                                                className="text-xs px-2 py-1 bg-purple-100 dark:bg-purple-900/30 text-purple-600 dark:text-purple-400 rounded"
                                            >
                                                <LogIn className="w-3 h-3 inline mr-1" />
                                                Impersonate
                                            </button>
                                        </td>
                                    </tr>
                                ))}
                            </tbody>
                        </table>

                        {filteredUsers.length === 0 && (
                            <div className="p-8 text-center text-slate-500">
                                No users found
                            </div>
                        )}
                    </div>
                </div>
            )}

            {/* Audit Log Tab */}
            {activeTab === 'audit' && (
                <div className="bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700 overflow-hidden">
                    <table className="w-full">
                        <thead className="bg-slate-50 dark:bg-slate-900/50">
                            <tr>
                                <th className="text-left p-3 text-sm font-medium text-slate-600 dark:text-slate-400">Time</th>
                                <th className="text-left p-3 text-sm font-medium text-slate-600 dark:text-slate-400">Action</th>
                                <th className="text-left p-3 text-sm font-medium text-slate-600 dark:text-slate-400">Target</th>
                                <th className="text-left p-3 text-sm font-medium text-slate-600 dark:text-slate-400">Details</th>
                            </tr>
                        </thead>
                        <tbody>
                            {auditLogs.map((log) => (
                                <tr key={log.id} className="border-t border-slate-100 dark:border-slate-700">
                                    <td className="p-3 text-sm text-slate-500">
                                        {new Date(log.created_at).toLocaleString()}
                                    </td>
                                    <td className="p-3">
                                        <span className="text-sm font-medium text-slate-700 dark:text-slate-300">
                                            {log.action_type}
                                        </span>
                                    </td>
                                    <td className="p-3 text-sm text-slate-600 dark:text-slate-400">
                                        {log.target_type}: {log.target_id?.substring(0, 8)}...
                                    </td>
                                    <td className="p-3 text-sm text-slate-500">
                                        <button
                                            onClick={() => alert(JSON.stringify(log.details, null, 2))}
                                            className="text-purple-500 hover:text-purple-600"
                                        >
                                            <Eye className="w-4 h-4" />
                                        </button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>

                    {auditLogs.length === 0 && (
                        <div className="p-8 text-center text-slate-500">
                            No audit logs yet
                        </div>
                    )}
                </div>
            )}
        </div>
    );
}
