import { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { Eye, ArrowRight, LayoutDashboard, CheckSquare, ClipboardCheck, Gamepad2, BookOpen, X } from 'lucide-react';

/**
 * ExploreLanding Component
 * Entry point for Explore Mode - read-only demo of the app
 */
export default function ExploreLanding() {
    const navigate = useNavigate();
    const [showFeatures, setShowFeatures] = useState(false);

    const features = [
        { icon: LayoutDashboard, title: 'Dashboard', description: 'Team overview and quick actions' },
        { icon: CheckSquare, title: 'Sprint Planning', description: 'Kanban board for task management' },
        { icon: ClipboardCheck, title: 'Scouting Reports', description: 'Track opponent capabilities' },
        { icon: Gamepad2, title: 'Match Planner', description: 'Plan your match strategy' },
        { icon: BookOpen, title: 'Portfolio Helper', description: 'AI-powered documentation' },
    ];

    const handleStartExplore = () => {
        // Set a flag in sessionStorage to indicate explore mode
        sessionStorage.setItem('explore_mode', 'true');
        navigate('/');
    };

    return (
        <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 flex items-center justify-center p-4">
            <div className="max-w-lg w-full">
                {/* Header */}
                <div className="text-center mb-8">
                    <div className="inline-flex items-center justify-center w-20 h-20 bg-gradient-to-br from-orange-500/20 to-amber-500/20 rounded-3xl mb-4">
                        <Eye className="w-10 h-10 text-orange-400" />
                    </div>
                    <h1 className="text-3xl font-bold text-white mb-2">Explore FalconForge</h1>
                    <p className="text-slate-400">
                        Take a tour of the app with sample data. No account required.
                    </p>
                </div>

                {/* Main Card */}
                <div className="bg-slate-800/50 backdrop-blur-xl border border-slate-700 rounded-2xl p-6 shadow-xl mb-4">
                    <div className="space-y-4">
                        <div className="bg-amber-500/10 border border-amber-500/30 rounded-xl p-4">
                            <h3 className="text-amber-300 font-medium mb-1">Read-Only Mode</h3>
                            <p className="text-amber-300/80 text-sm">
                                Explore mode uses sample data. Changes won't be saved.
                            </p>
                        </div>

                        <button
                            onClick={handleStartExplore}
                            className="w-full flex items-center justify-center gap-2 bg-gradient-to-r from-orange-500 to-orange-600 text-white font-semibold py-4 px-6 rounded-xl hover:from-orange-600 hover:to-orange-700 transition-all shadow-lg shadow-orange-500/25"
                        >
                            <Eye className="w-5 h-5" />
                            Start Exploring
                            <ArrowRight className="w-5 h-5" />
                        </button>

                        <button
                            onClick={() => setShowFeatures(!showFeatures)}
                            className="w-full text-center text-slate-400 hover:text-slate-200 text-sm py-2 transition-colors"
                        >
                            {showFeatures ? 'Hide features' : 'What can I explore?'}
                        </button>

                        {showFeatures && (
                            <div className="space-y-2 pt-2 border-t border-slate-700">
                                {features.map((feature, i) => (
                                    <div key={i} className="flex items-center gap-3 p-2">
                                        <div className="w-8 h-8 bg-slate-700 rounded-lg flex items-center justify-center">
                                            <feature.icon className="w-4 h-4 text-orange-400" />
                                        </div>
                                        <div>
                                            <p className="text-white text-sm font-medium">{feature.title}</p>
                                            <p className="text-slate-500 text-xs">{feature.description}</p>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        )}
                    </div>
                </div>

                {/* Sign Up CTA */}
                <div className="text-center">
                    <p className="text-slate-500 text-sm mb-2">Ready to use FalconForge with your team?</p>
                    <button
                        onClick={() => navigate('/login')}
                        className="text-orange-400 hover:text-orange-300 font-medium text-sm"
                    >
                        Create an account 
                    </button>
                </div>
            </div>
        </div>
    );
}

/**
 * ExploreBanner Component
 * Shows at the top of the app when in explore mode
 */
export function ExploreBanner({ onExit }: { onExit: () => void }) {
    return (
        <div className="bg-gradient-to-r from-amber-500 to-orange-500 text-white px-4 py-2 flex items-center justify-between">
            <div className="flex items-center gap-2">
                <Eye className="w-4 h-4" />
                <span className="text-sm font-medium">
                    Explore Mode - Using sample data (changes won't be saved)
                </span>
            </div>
            <button
                onClick={onExit}
                className="flex items-center gap-1 text-sm bg-white/20 hover:bg-white/30 px-3 py-1 rounded-lg transition-colors"
            >
                <X className="w-4 h-4" />
                Exit
            </button>
        </div>
    );
}
