import { ArrowLeft } from 'lucide-react';
import { Link } from 'react-router-dom';

export default function ParentalConsent() {
    return (
        <div className="min-h-screen bg-gradient-to-br from-slate-900 via-slate-800 to-slate-900 py-12 px-4">
            <div className="max-w-3xl mx-auto">
                <Link
                    to="/login"
                    className="inline-flex items-center gap-2 text-slate-400 hover:text-white mb-8 transition-colors"
                >
                    <ArrowLeft className="w-4 h-4" />
                    Back to Login
                </Link>

                <div className="bg-slate-800 rounded-xl border border-slate-700 p-8">
                    <h1 className="text-3xl font-bold text-white mb-6">Parental Consent Information</h1>

                    <div className="prose prose-invert prose-slate max-w-none">
                        <p className="text-slate-300">
                            Last updated: {new Date().toLocaleDateString()}
                        </p>

                        <div className="bg-amber-900/30 border border-amber-700 rounded-lg p-4 my-6">
                            <p className="text-amber-200 font-medium">
                                This information is for parents/guardians of students under 18 who wish to use FalconForge.
                            </p>
                        </div>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">About FalconForge</h2>
                        <p className="text-slate-300">
                            FalconForge is a team management platform designed for FIRST Tech Challenge robotics teams. It helps teams organize tasks, track progress, and prepare for competitions.
                        </p>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">Information Collected from Students</h2>
                        <p className="text-slate-300">
                            When students use FalconForge, we collect:
                        </p>
                        <ul className="text-slate-300 list-disc pl-6 space-y-2">
                            <li>Name and email address</li>
                            <li>Team role and sub-team assignments</li>
                            <li>Tasks and project contributions</li>
                            <li>Scouting observations and notes</li>
                        </ul>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">How Student Data is Protected</h2>
                        <ul className="text-slate-300 list-disc pl-6 space-y-2">
                            <li>All data is encrypted in transit and at rest</li>
                            <li>Student data is only visible to approved team members</li>
                            <li>Coaches/mentors must verify their identity</li>
                            <li>We do not sell or share student data with third parties</li>
                            <li>Students cannot be contacted by users outside their team</li>
                        </ul>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">Coach/Mentor Supervision</h2>
                        <p className="text-slate-300">
                            All student accounts are associated with a team managed by verified adult coaches or mentors. These adults are responsible for:
                        </p>
                        <ul className="text-slate-300 list-disc pl-6 space-y-2">
                            <li>Approving student membership requests</li>
                            <li>Supervising team activity</li>
                            <li>Removing inappropriate content</li>
                            <li>Managing team access</li>
                        </ul>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">Your Rights as a Parent/Guardian</h2>
                        <p className="text-slate-300">
                            You have the right to:
                        </p>
                        <ul className="text-slate-300 list-disc pl-6 space-y-2">
                            <li>Review your child's data</li>
                            <li>Request deletion of your child's account</li>
                            <li>Withdraw consent at any time</li>
                            <li>Contact us with any concerns</li>
                        </ul>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">Consent Process</h2>
                        <p className="text-slate-300">
                            By checking the parental consent checkbox during student signup, you confirm that:
                        </p>
                        <ul className="text-slate-300 list-disc pl-6 space-y-2">
                            <li>You are the parent/guardian of the student</li>
                            <li>You consent to the collection of the data described above</li>
                            <li>You have reviewed the <Link to="/terms" className="text-orange-400 hover:text-orange-300">Terms of Service</Link> and <Link to="/privacy" className="text-orange-400 hover:text-orange-300">Privacy Policy</Link></li>
                        </ul>

                        <h2 className="text-xl font-semibold text-white mt-6 mb-3">Contact</h2>
                        <p className="text-slate-300">
                            For questions about student privacy, contact us at privacy@falconforge.app
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
}
