import { useState, useRef, useEffect } from 'react';
import { useOrganization } from '../lib/organization-context';
import { ChevronDown, Building2, Check, Plus } from 'lucide-react';

interface TeamSwitcherProps {
    onCreateTeam?: () => void;
}

export default function TeamSwitcher({ onCreateTeam }: TeamSwitcherProps) {
    const { organizations, currentOrganization, setCurrentOrganization } = useOrganization();
    const [isOpen, setIsOpen] = useState(false);
    const dropdownRef = useRef<HTMLDivElement>(null);

    // Close dropdown when clicking outside
    useEffect(() => {
        const handleClickOutside = (event: MouseEvent) => {
            if (dropdownRef.current && !dropdownRef.current.contains(event.target as Node)) {
                setIsOpen(false);
            }
        };

        document.addEventListener('mousedown', handleClickOutside);
        return () => document.removeEventListener('mousedown', handleClickOutside);
    }, []);

    // Don't show if no organizations
    if (organizations.length === 0) {
        return null;
    }

    // Show single org with option to add more
    if (organizations.length === 1) {
        return (
            <div className="space-y-2">
                <div className="flex items-center gap-2 px-3 py-2 bg-slate-100 dark:bg-slate-700 rounded-lg">
                    <Building2 className="w-4 h-4 text-orange-500" />
                    <div className="text-sm flex-1">
                        <span className="font-medium text-slate-700 dark:text-white">
                            {currentOrganization?.name || 'No Team'}
                        </span>
                        {currentOrganization?.team_number && (
                            <span className="text-slate-500 dark:text-slate-400 ml-1">
                                #{currentOrganization.team_number}
                            </span>
                        )}
                    </div>
                </div>
                {onCreateTeam && (
                    <button
                        onClick={onCreateTeam}
                        className="w-full flex items-center justify-center gap-2 px-3 py-1.5 text-xs font-medium text-orange-600 dark:text-orange-400 bg-orange-50 dark:bg-orange-900/20 rounded-lg hover:bg-orange-100 dark:hover:bg-orange-900/40 transition-colors"
                    >
                        <Plus className="w-3 h-3" />
                        Add Another Team
                    </button>
                )}
            </div>
        );
    }

    return (
        <div ref={dropdownRef} className="relative">
            <button
                onClick={() => setIsOpen(!isOpen)}
                className="flex items-center gap-2 px-3 py-2 bg-slate-100 dark:bg-slate-700 hover:bg-slate-200 dark:hover:bg-slate-600 rounded-lg transition-colors w-full"
            >
                <Building2 className="w-4 h-4 text-orange-500" />
                <div className="text-sm text-left flex-1 min-w-0">
                    <span className="font-medium text-slate-700 dark:text-white truncate block">
                        {currentOrganization?.name || 'Select Team'}
                    </span>
                    {currentOrganization?.team_number && (
                        <span className="text-slate-500 dark:text-slate-400 text-xs">
                            #{currentOrganization.team_number}
                        </span>
                    )}
                </div>
                <ChevronDown className={`w-4 h-4 text-slate-400 transition-transform flex-shrink-0 ${isOpen ? 'rotate-180' : ''}`} />
            </button>

            {isOpen && (
                <div className="absolute top-full left-0 mt-1 w-64 bg-slate-800 border border-slate-700 rounded-xl shadow-xl z-50 overflow-hidden">
                    <div className="py-1">
                        {organizations.map((org) => (
                            <button
                                key={org.organization.id}
                                onClick={() => {
                                    setCurrentOrganization(org.organization.id);
                                    setIsOpen(false);
                                }}
                                className={`w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-slate-700/50 transition-colors ${currentOrganization?.id === org.organization.id
                                    ? 'bg-slate-700/30'
                                    : ''
                                    }`}
                            >
                                <div className="w-8 h-8 bg-orange-500/20 rounded-lg flex items-center justify-center flex-shrink-0">
                                    <Building2 className="w-4 h-4 text-orange-400" />
                                </div>
                                <div className="flex-1 min-w-0">
                                    <p className="font-medium text-white truncate">
                                        {org.organization.name}
                                    </p>
                                    <p className="text-xs text-slate-400">
                                        {org.organization.team_number && `Team #${org.organization.team_number}  `}
                                        {org.membership.role}
                                    </p>
                                </div>
                                {currentOrganization?.id === org.organization.id && (
                                    <Check className="w-4 h-4 text-green-400 flex-shrink-0" />
                                )}
                            </button>
                        ))}
                    </div>

                    {onCreateTeam && (
                        <>
                            <div className="border-t border-slate-700" />
                            <button
                                onClick={() => {
                                    onCreateTeam();
                                    setIsOpen(false);
                                }}
                                className="w-full flex items-center gap-3 px-4 py-2.5 text-left hover:bg-slate-700/50 transition-colors text-orange-400"
                            >
                                <Plus className="w-4 h-4" />
                                <span className="text-sm font-medium">Create New Team</span>
                            </button>
                        </>
                    )}
                </div>
            )}
        </div>
    );
}
