import { useState, useEffect } from 'react';
import { useOrganization } from '../lib/organization-context';
import { useAuth } from '../lib/auth';
import { supabase } from '../lib/supabase';
import {
    Users, UserPlus, UserMinus, Check, X, Clock, Shield,
    GraduationCap, User, Loader2, Link2, Copy, RefreshCw,
    AlertTriangle, Building2, Pencil, ArrowRightLeft, AlertCircle
} from 'lucide-react';
import type { OrganizationMember, OrganizationRole, OrganizationInvite } from '../lib/database.types';

interface MemberWithUser extends OrganizationMember {
    user?: {
        id: string;
        email: string;
        full_name: string | null;
    };
}

const ROLE_LABELS: Record<OrganizationRole, string> = {
    owner: 'Owner',
    coach: 'Coach',
    mentor: 'Mentor',
    student: 'Student',
};

const ROLE_COLORS: Record<OrganizationRole, string> = {
    owner: 'bg-purple-100 text-purple-700 dark:bg-purple-900/30 dark:text-purple-300',
    coach: 'bg-orange-100 text-orange-700 dark:bg-orange-900/30 dark:text-orange-300',
    mentor: 'bg-blue-100 text-blue-700 dark:bg-blue-900/30 dark:text-blue-300',
    student: 'bg-green-100 text-green-700 dark:bg-green-900/30 dark:text-green-300',
};

export default function TeamManagement() {
    const { currentOrganization, isAdmin, isOwner, refreshOrganizations } = useOrganization();
    const { user } = useAuth();

    const [members, setMembers] = useState<MemberWithUser[]>([]);
    const [invites, setInvites] = useState<OrganizationInvite[]>([]);
    const [isLoading, setIsLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);
    const [successMessage, setSuccessMessage] = useState<string | null>(null);

    // Invite creation state
    const [showCreateInvite, setShowCreateInvite] = useState(false);
    const [newInviteRole, setNewInviteRole] = useState<OrganizationRole>('student');
    const [newInviteMaxUses, setNewInviteMaxUses] = useState<number | null>(null);
    const [newInviteExpiresDays, setNewInviteExpiresDays] = useState<number | null>(7);
    const [isCreatingInvite, setIsCreatingInvite] = useState(false);
    const [copiedInviteId, setCopiedInviteId] = useState<string | null>(null);

    // Organization edit state
    const [showEditOrg, setShowEditOrg] = useState(false);
    const [editOrgName, setEditOrgName] = useState('');
    const [editOrgTeamNumber, setEditOrgTeamNumber] = useState('');
    const [isSavingOrg, setIsSavingOrg] = useState(false);

    // Ownership transfer state
    const [showTransfer, setShowTransfer] = useState(false);
    const [transferToUserId, setTransferToUserId] = useState('');
    const [isCreatingTransfer, setIsCreatingTransfer] = useState(false);
    const [pendingTransfers, setPendingTransfers] = useState<any[]>([]);
    const [incomingTransfers, setIncomingTransfers] = useState<any[]>([]);
    const [ackBilling, setAckBilling] = useState(false);
    const [isAcceptingTransfer, setIsAcceptingTransfer] = useState(false);

    // Load members and invites
    useEffect(() => {
        if (!currentOrganization || !supabase) return;
        loadData();
    }, [currentOrganization]);

    const loadData = async () => {
        if (!currentOrganization || !supabase) return;

        setIsLoading(true);
        setError(null);

        try {
            const client = supabase as any;

            // Load members first (without user join)
            const { data: membersData, error: membersError } = await client
                .from('organization_members')
                .select('*')
                .eq('organization_id', currentOrganization.id)
                .in('status', ['pending', 'approved', 'suspended']);

            if (membersError) throw membersError;

            // Get unique user IDs
            const userIds = [...new Set((membersData || []).map((m: any) => m.user_id))];

            // Fetch user data separately
            let usersMap: Record<string, { id: string; email: string; full_name: string | null }> = {};
            if (userIds.length > 0) {
                const { data: usersData, error: usersError } = await client
                    .from('users')
                    .select('id, email, full_name')
                    .in('id', userIds);

                if (!usersError && usersData) {
                    usersMap = Object.fromEntries(
                        usersData.map((u: any) => [u.id, u])
                    );
                }
            }

            // Merge user data with members
            const membersWithUsers = (membersData || []).map((member: any) => ({
                ...member,
                user: usersMap[member.user_id] || null,
            }));

            setMembers(membersWithUsers);

            // Load active invites if admin
            if (isAdmin) {
                const { data: invitesData, error: invitesError } = await client
                    .from('organization_invites')
                    .select('*')
                    .eq('organization_id', currentOrganization.id)
                    .eq('active', true);

                if (invitesError) throw invitesError;
                setInvites(invitesData || []);

                // Load ownership transfer requests
                const { data: outgoingTransfers } = await client
                    .from('ownership_transfer_requests')
                    .select('*')
                    .eq('organization_id', currentOrganization.id)
                    .eq('from_user_id', user?.id)
                    .eq('status', 'pending');
                setPendingTransfers(outgoingTransfers || []);
            }

            // Load incoming transfers for this user
            if (user) {
                const { data: incoming } = await client
                    .from('ownership_transfer_requests')
                    .select('*, organization:organizations(id, name, team_number)')
                    .eq('to_user_id', user.id)
                    .eq('status', 'pending');
                setIncomingTransfers(incoming || []);
            }
        } catch (err: any) {
            console.error('Error loading team data:', err);
            setError(err.message || 'Failed to load team data');
        } finally {
            setIsLoading(false);
        }
    };

    // Approve a pending member
    const approveMember = async (memberId: string) => {
        if (!supabase || !user || !currentOrganization) return;

        try {
            const client = supabase as any;
            const { error } = await client
                .from('organization_members')
                .update({
                    status: 'approved',
                    approved_at: new Date().toISOString(),
                    approved_by: user.id,
                })
                .eq('id', memberId)
                .eq('organization_id', currentOrganization.id);

            if (error) throw error;
            await loadData();
        } catch (err: any) {
            console.error('Error approving member:', err);
            setError(err.message);
        }
    };

    // Reject/remove a member
    const removeMember = async (memberId: string, reason?: string) => {
        if (!supabase || !user || !currentOrganization) return;

        try {
            const client = supabase as any;
            const { error } = await client
                .from('organization_members')
                .update({
                    status: 'removed',
                    removed_at: new Date().toISOString(),
                    removed_by: user.id,
                    removal_reason: reason || null,
                })
                .eq('id', memberId)
                .eq('organization_id', currentOrganization.id);

            if (error) throw error;
            await loadData();
        } catch (err: any) {
            console.error('Error removing member:', err);
            setError(err.message);
        }
    };

    // Update member role
    const updateMemberRole = async (memberId: string, newRole: OrganizationRole) => {
        if (!supabase || !currentOrganization) return;

        try {
            const client = supabase as any;
            const { error } = await client
                .from('organization_members')
                .update({ role: newRole })
                .eq('id', memberId)
                .eq('organization_id', currentOrganization.id);

            if (error) throw error;
            await loadData();
        } catch (err: any) {
            console.error('Error updating role:', err);
            setError(err.message);
        }
    };

    // Create an invite link
    const createInvite = async () => {
        if (!supabase || !user || !currentOrganization) return;

        setIsCreatingInvite(true);
        setError(null);

        try {
            const client = supabase as any;

            // Generate a unique invite code
            const inviteCode = Math.random().toString(36).substring(2, 8).toUpperCase();

            // Calculate expiration
            let expiresAt: string | null = null;
            if (newInviteExpiresDays) {
                const expires = new Date();
                expires.setDate(expires.getDate() + newInviteExpiresDays);
                expiresAt = expires.toISOString();
            }

            const { error } = await client
                .from('organization_invites')
                .insert({
                    organization_id: currentOrganization.id,
                    invite_code: inviteCode,
                    created_by: user.id,
                    default_role: newInviteRole,
                    max_uses: newInviteMaxUses,
                    expires_at: expiresAt,
                    active: true,
                });

            if (error) throw error;

            await loadData();
            setShowCreateInvite(false);
        } catch (err: any) {
            console.error('Error creating invite:', err);
            setError(err.message);
        } finally {
            setIsCreatingInvite(false);
        }
    };

    // Deactivate an invite
    const deactivateInvite = async (inviteId: string) => {
        if (!supabase || !currentOrganization) return;

        try {
            const client = supabase as any;
            const { error } = await client
                .from('organization_invites')
                .update({ active: false })
                .eq('id', inviteId)
                .eq('organization_id', currentOrganization.id);

            if (error) throw error;
            await loadData();
        } catch (err: any) {
            console.error('Error deactivating invite:', err);
            setError(err.message);
        }
    };

    // Copy invite link
    const copyInviteLink = (inviteCode: string, inviteId: string) => {
        const link = `${window.location.origin}/#/join/${inviteCode}`;
        navigator.clipboard.writeText(link);
        setCopiedInviteId(inviteId);
        setTimeout(() => setCopiedInviteId(null), 2000);
    };

    // Save organization details
    const saveOrganization = async () => {
        if (!supabase || !currentOrganization) return;

        setIsSavingOrg(true);
        setError(null);

        try {
            const client = supabase as any;
            const { error } = await client
                .from('organizations')
                .update({
                    name: editOrgName,
                    team_number: editOrgTeamNumber || null,
                })
                .eq('id', currentOrganization.id);

            if (error) throw error;

            setSuccessMessage('Organization updated successfully');
            setShowEditOrg(false);
            await refreshOrganizations();
            setTimeout(() => setSuccessMessage(null), 3000);
        } catch (err: any) {
            console.error('Error updating organization:', err);
            setError(err.message);
        } finally {
            setIsSavingOrg(false);
        }
    };

    // Create ownership transfer request
    const createTransferRequest = async () => {
        if (!supabase || !user || !currentOrganization || !transferToUserId) return;

        setIsCreatingTransfer(true);
        setError(null);

        try {
            const client = supabase as any;
            const { error } = await client
                .from('ownership_transfer_requests')
                .insert({
                    organization_id: currentOrganization.id,
                    from_user_id: user.id,
                    to_user_id: transferToUserId,
                    status: 'pending',
                });

            if (error) throw error;

            setSuccessMessage('Transfer request sent');
            setShowTransfer(false);
            setTransferToUserId('');
            await loadData();
            setTimeout(() => setSuccessMessage(null), 3000);
        } catch (err: any) {
            console.error('Error creating transfer:', err);
            setError(err.message);
        } finally {
            setIsCreatingTransfer(false);
        }
    };

    // Accept incoming transfer
    const acceptTransfer = async (transferId: string) => {
        if (!supabase || !ackBilling) return;

        setIsAcceptingTransfer(true);
        setError(null);

        try {
            const client = supabase as any;
            const { error } = await client.rpc('accept_ownership_transfer', {
                transfer_id: transferId,
                acknowledge_billing: true,
            });

            if (error) throw error;

            setSuccessMessage('Ownership transferred successfully!');
            setAckBilling(false);
            await refreshOrganizations();
            await loadData();
            setTimeout(() => setSuccessMessage(null), 3000);
        } catch (err: any) {
            console.error('Error accepting transfer:', err);
            setError(err.message);
        } finally {
            setIsAcceptingTransfer(false);
        }
    };

    // Decline incoming transfer
    const declineTransfer = async (transferId: string) => {
        if (!supabase) return;

        try {
            const client = supabase as any;
            await client
                .from('ownership_transfer_requests')
                .update({ status: 'declined' })
                .eq('id', transferId);

            await loadData();
        } catch (err: any) {
            console.error('Error declining transfer:', err);
            setError(err.message);
        }
    };

    // Cancel outgoing transfer
    const cancelTransfer = async (transferId: string) => {
        if (!supabase) return;

        try {
            const client = supabase as any;
            await client
                .from('ownership_transfer_requests')
                .update({ status: 'cancelled' })
                .eq('id', transferId);

            await loadData();
        } catch (err: any) {
            console.error('Error canceling transfer:', err);
            setError(err.message);
        }
    };


    if (!currentOrganization) {
        return (
            <div className="p-6 bg-white dark:bg-slate-800 rounded-xl border border-slate-200 dark:border-slate-700">
                <p className="text-slate-500 dark:text-slate-400">
                    No organization selected
                </p>
            </div>
        );
    }

    const pendingMembers = members.filter(m => m.status === 'pending');
    const activeMembers = members.filter(m => m.status === 'approved');

    return (
        <div className="space-y-6">
            {/* Organization Header */}
            <div className="flex items-center justify-between">
                <div className="flex items-center gap-3">
                    <div className="w-12 h-12 bg-orange-100 dark:bg-orange-900/30 rounded-xl flex items-center justify-center">
                        <Building2 className="w-6 h-6 text-orange-600 dark:text-orange-400" />
                    </div>
                    <div>
                        <h2 className="text-xl font-bold text-slate-900 dark:text-white">
                            {currentOrganization.name}
                        </h2>
                        {currentOrganization.team_number && (
                            <p className="text-sm text-slate-500 dark:text-slate-400">
                                Team #{currentOrganization.team_number}
                            </p>
                        )}
                    </div>
                </div>
                <button
                    onClick={loadData}
                    disabled={isLoading}
                    className="p-2 text-slate-500 hover:text-slate-700 dark:text-slate-400 dark:hover:text-slate-200"
                >
                    <RefreshCw className={`w-5 h-5 ${isLoading ? 'animate-spin' : ''}`} />
                </button>
            </div>

            {/* Error Display */}
            {error && (
                <div className="bg-red-50 dark:bg-red-900/20 border border-red-200 dark:border-red-800 rounded-xl p-4 flex items-start gap-3">
                    <AlertTriangle className="w-5 h-5 text-red-500 flex-shrink-0 mt-0.5" />
                    <p className="text-red-700 dark:text-red-300 text-sm">{error}</p>
                </div>
            )}

            {/* Success Message */}
            {successMessage && (
                <div className="bg-green-50 dark:bg-green-900/20 border border-green-200 dark:border-green-800 rounded-xl p-4 flex items-start gap-3">
                    <Check className="w-5 h-5 text-green-500 flex-shrink-0 mt-0.5" />
                    <p className="text-green-700 dark:text-green-300 text-sm">{successMessage}</p>
                </div>
            )}

            {/* Incoming Transfer Requests */}
            {incomingTransfers.length > 0 && (
                <div className="bg-purple-50 dark:bg-purple-900/20 border border-purple-200 dark:border-purple-800 rounded-xl p-4">
                    <h3 className="text-purple-800 dark:text-purple-200 font-semibold mb-3 flex items-center gap-2">
                        <ArrowRightLeft className="w-5 h-5" />
                        Incoming Ownership Transfer
                    </h3>
                    {incomingTransfers.map((transfer) => (
                        <div key={transfer.id} className="bg-white dark:bg-slate-800 rounded-lg p-4 space-y-3">
                            <p className="text-slate-700 dark:text-slate-300">
                                You've been invited to become the owner of{' '}
                                <strong>{transfer.organization?.name}</strong>
                                {transfer.organization?.team_number && ` (Team #${transfer.organization.team_number})`}
                            </p>

                            <div className="bg-amber-50 dark:bg-amber-900/30 border border-amber-200 dark:border-amber-700 rounded-lg p-3">
                                <div className="flex items-start gap-2">
                                    <AlertCircle className="w-5 h-5 text-amber-600 flex-shrink-0 mt-0.5" />
                                    <div>
                                        <p className="text-amber-800 dark:text-amber-200 text-sm font-medium">
                                            Billing Responsibility
                                        </p>
                                        <p className="text-amber-700 dark:text-amber-300 text-sm mt-1">
                                            By accepting ownership, you will be responsible for all future billing charges for this team's subscription.
                                        </p>
                                    </div>
                                </div>
                            </div>

                            <label className="flex items-center gap-2 cursor-pointer">
                                <input
                                    type="checkbox"
                                    checked={ackBilling}
                                    onChange={(e) => setAckBilling(e.target.checked)}
                                    className="w-4 h-4 rounded border-slate-300 text-orange-500 focus:ring-orange-500"
                                />
                                <span className="text-sm text-slate-700 dark:text-slate-300">
                                    I acknowledge I will be responsible for future billing
                                </span>
                            </label>

                            <div className="flex gap-2">
                                <button
                                    onClick={() => acceptTransfer(transfer.id)}
                                    disabled={!ackBilling || isAcceptingTransfer}
                                    className="flex items-center gap-2 px-4 py-2 bg-green-500 hover:bg-green-600 text-white rounded-lg disabled:opacity-50"
                                >
                                    {isAcceptingTransfer ? (
                                        <Loader2 className="w-4 h-4 animate-spin" />
                                    ) : (
                                        <Check className="w-4 h-4" />
                                    )}
                                    Accept Ownership
                                </button>
                                <button
                                    onClick={() => declineTransfer(transfer.id)}
                                    className="px-4 py-2 bg-slate-200 dark:bg-slate-700 text-slate-700 dark:text-slate-300 rounded-lg hover:bg-slate-300 dark:hover:bg-slate-600"
                                >
                                    Decline
                                </button>
                            </div>
                        </div>
                    ))}
                </div>
            )}

            {/* Organization Settings (Admin Only) */}
            {isAdmin && (
                <div className="bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded-xl overflow-hidden">
                    <div className="p-4 border-b border-slate-200 dark:border-slate-700 flex items-center justify-between">
                        <h3 className="font-semibold text-slate-900 dark:text-white flex items-center gap-2">
                            <Pencil className="w-5 h-5 text-slate-500" />
                            Organization Settings
                        </h3>
                        {!showEditOrg && (
                            <button
                                onClick={() => {
                                    setEditOrgName(currentOrganization.name);
                                    setEditOrgTeamNumber(currentOrganization.team_number || '');
                                    setShowEditOrg(true);
                                }}
                                className="text-sm text-orange-500 hover:text-orange-600 font-medium"
                            >
                                Edit
                            </button>
                        )}
                    </div>

                    {showEditOrg ? (
                        <div className="p-4 space-y-4">
                            <div>
                                <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-1">
                                    Team Name
                                </label>
                                <input
                                    type="text"
                                    value={editOrgName}
                                    onChange={(e) => setEditOrgName(e.target.value)}
                                    className="w-full px-3 py-2 bg-slate-50 dark:bg-slate-700 border border-slate-200 dark:border-slate-600 rounded-lg"
                                />
                            </div>
                            <div>
                                <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-1">
                                    Team Number
                                </label>
                                <input
                                    type="text"
                                    value={editOrgTeamNumber}
                                    onChange={(e) => setEditOrgTeamNumber(e.target.value)}
                                    className="w-full px-3 py-2 bg-slate-50 dark:bg-slate-700 border border-slate-200 dark:border-slate-600 rounded-lg"
                                    placeholder="Optional"
                                />
                            </div>
                            <div className="flex gap-2">
                                <button
                                    onClick={() => setShowEditOrg(false)}
                                    className="px-4 py-2 bg-slate-200 dark:bg-slate-700 text-slate-700 dark:text-slate-300 rounded-lg"
                                >
                                    Cancel
                                </button>
                                <button
                                    onClick={saveOrganization}
                                    disabled={isSavingOrg || !editOrgName.trim()}
                                    className="flex items-center gap-2 px-4 py-2 bg-orange-500 hover:bg-orange-600 text-white rounded-lg disabled:opacity-50"
                                >
                                    {isSavingOrg ? <Loader2 className="w-4 h-4 animate-spin" /> : <Check className="w-4 h-4" />}
                                    Save
                                </button>
                            </div>
                        </div>
                    ) : (
                        <div className="p-4 text-sm text-slate-600 dark:text-slate-400">
                            <p><strong>Name:</strong> {currentOrganization.name}</p>
                            <p><strong>Team Number:</strong> {currentOrganization.team_number || 'Not set'}</p>
                        </div>
                    )}
                </div>
            )}

            {/* Transfer Ownership (Owner Only) */}
            {isOwner && (
                <div className="bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded-xl overflow-hidden">
                    <div className="p-4 border-b border-slate-200 dark:border-slate-700 flex items-center justify-between">
                        <h3 className="font-semibold text-slate-900 dark:text-white flex items-center gap-2">
                            <ArrowRightLeft className="w-5 h-5 text-slate-500" />
                            Transfer Ownership
                        </h3>
                    </div>

                    {pendingTransfers.length > 0 ? (
                        <div className="p-4">
                            <p className="text-slate-600 dark:text-slate-400 text-sm mb-3">
                                You have a pending transfer request. Waiting for the new owner to accept.
                            </p>
                            {pendingTransfers.map((transfer) => (
                                <div key={transfer.id} className="flex items-center justify-between bg-slate-50 dark:bg-slate-700 rounded-lg p-3">
                                    <span className="text-sm text-slate-700 dark:text-slate-300">
                                        Transfer to: {members.find(m => m.user_id === transfer.to_user_id)?.user?.full_name || transfer.to_user_id}
                                    </span>
                                    <button
                                        onClick={() => cancelTransfer(transfer.id)}
                                        className="text-sm text-red-500 hover:text-red-600"
                                    >
                                        Cancel
                                    </button>
                                </div>
                            ))}
                        </div>
                    ) : showTransfer ? (
                        <div className="p-4 space-y-4">
                            <div className="bg-amber-50 dark:bg-amber-900/30 border border-amber-200 dark:border-amber-700 rounded-lg p-3">
                                <p className="text-amber-800 dark:text-amber-200 text-sm">
                                    <strong>Warning:</strong> Transferring ownership will make the selected coach responsible for all billing.
                                </p>
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-1">
                                    Select New Owner
                                </label>
                                <select
                                    value={transferToUserId}
                                    onChange={(e) => setTransferToUserId(e.target.value)}
                                    className="w-full px-3 py-2 bg-slate-50 dark:bg-slate-700 border border-slate-200 dark:border-slate-600 rounded-lg"
                                >
                                    <option value="">Select a coach...</option>
                                    {activeMembers
                                        .filter(m => m.role === 'coach' && m.user_id !== user?.id)
                                        .map((coach) => (
                                            <option key={coach.user_id} value={coach.user_id}>
                                                {coach.user?.full_name || coach.user?.email || coach.user_id}
                                            </option>
                                        ))}
                                </select>
                                {activeMembers.filter(m => m.role === 'coach' && m.user_id !== user?.id).length === 0 && (
                                    <p className="text-slate-500 text-xs mt-1">
                                        No other coaches available. Promote a member to coach first.
                                    </p>
                                )}
                            </div>

                            <div className="flex gap-2">
                                <button
                                    onClick={() => setShowTransfer(false)}
                                    className="px-4 py-2 bg-slate-200 dark:bg-slate-700 text-slate-700 dark:text-slate-300 rounded-lg"
                                >
                                    Cancel
                                </button>
                                <button
                                    onClick={createTransferRequest}
                                    disabled={isCreatingTransfer || !transferToUserId}
                                    className="flex items-center gap-2 px-4 py-2 bg-orange-500 hover:bg-orange-600 text-white rounded-lg disabled:opacity-50"
                                >
                                    {isCreatingTransfer ? <Loader2 className="w-4 h-4 animate-spin" /> : <ArrowRightLeft className="w-4 h-4" />}
                                    Send Transfer Request
                                </button>
                            </div>
                        </div>
                    ) : (
                        <div className="p-4">
                            <p className="text-slate-600 dark:text-slate-400 text-sm mb-3">
                                Transfer ownership of this team to another coach.
                            </p>
                            <button
                                onClick={() => setShowTransfer(true)}
                                className="text-sm text-orange-500 hover:text-orange-600 font-medium"
                            >
                                Initiate Transfer
                            </button>
                        </div>
                    )}
                </div>
            )}

            {/* Pending Approvals */}
            {isAdmin && pendingMembers.length > 0 && (
                <div className="bg-amber-50 dark:bg-amber-900/20 border border-amber-200 dark:border-amber-800 rounded-xl p-4">
                    <h3 className="text-amber-800 dark:text-amber-200 font-semibold mb-3 flex items-center gap-2">
                        <Clock className="w-5 h-5" />
                        Pending Approvals ({pendingMembers.length})
                    </h3>
                    <div className="space-y-2">
                        {pendingMembers.map(member => (
                            <div
                                key={member.id}
                                className="bg-white dark:bg-slate-800 rounded-lg p-3 flex items-center justify-between"
                            >
                                <div className="flex items-center gap-3">
                                    <div className="w-10 h-10 bg-slate-100 dark:bg-slate-700 rounded-full flex items-center justify-center">
                                        <User className="w-5 h-5 text-slate-500" />
                                    </div>
                                    <div>
                                        <p className="font-medium text-slate-900 dark:text-white">
                                            {member.user?.full_name || member.user?.email || 'Unknown'}
                                        </p>
                                        <p className="text-sm text-slate-500 dark:text-slate-400">
                                            {member.user?.email}
                                        </p>
                                    </div>
                                    <span className={`text-xs px-2 py-1 rounded-full ${ROLE_COLORS[member.role]}`}>
                                        {ROLE_LABELS[member.role]}
                                    </span>
                                </div>
                                <div className="flex items-center gap-2">
                                    <button
                                        onClick={() => approveMember(member.id)}
                                        className="p-2 bg-green-100 hover:bg-green-200 dark:bg-green-900/30 dark:hover:bg-green-900/50 text-green-600 dark:text-green-400 rounded-lg"
                                        title="Approve"
                                    >
                                        <Check className="w-4 h-4" />
                                    </button>
                                    <button
                                        onClick={() => removeMember(member.id, 'Request denied')}
                                        className="p-2 bg-red-100 hover:bg-red-200 dark:bg-red-900/30 dark:hover:bg-red-900/50 text-red-600 dark:text-red-400 rounded-lg"
                                        title="Deny"
                                    >
                                        <X className="w-4 h-4" />
                                    </button>
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            )}

            {/* Active Members */}
            <div className="bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded-xl overflow-hidden">
                <div className="p-4 border-b border-slate-200 dark:border-slate-700 flex items-center justify-between">
                    <h3 className="font-semibold text-slate-900 dark:text-white flex items-center gap-2">
                        <Users className="w-5 h-5 text-slate-500" />
                        Team Members ({activeMembers.length})
                    </h3>
                </div>

                {isLoading ? (
                    <div className="p-8 flex items-center justify-center">
                        <Loader2 className="w-6 h-6 animate-spin text-slate-400" />
                    </div>
                ) : activeMembers.length === 0 ? (
                    <div className="p-8 text-center text-slate-500 dark:text-slate-400">
                        No team members yet
                    </div>
                ) : (
                    <div className="divide-y divide-slate-100 dark:divide-slate-700">
                        {activeMembers.map(member => (
                            <div
                                key={member.id}
                                className="p-4 flex items-center justify-between hover:bg-slate-50 dark:hover:bg-slate-700/50"
                            >
                                <div className="flex items-center gap-3">
                                    <div className="w-10 h-10 bg-slate-100 dark:bg-slate-700 rounded-full flex items-center justify-center">
                                        {member.role === 'owner' || member.role === 'coach' ? (
                                            <Shield className="w-5 h-5 text-orange-500" />
                                        ) : member.role === 'mentor' ? (
                                            <GraduationCap className="w-5 h-5 text-blue-500" />
                                        ) : (
                                            <User className="w-5 h-5 text-slate-500" />
                                        )}
                                    </div>
                                    <div>
                                        <p className="font-medium text-slate-900 dark:text-white">
                                            {member.user?.full_name || member.user?.email || 'Unknown'}
                                            {member.user_id === user?.id && (
                                                <span className="ml-2 text-xs text-slate-500">(you)</span>
                                            )}
                                        </p>
                                        <p className="text-sm text-slate-500 dark:text-slate-400">
                                            {member.user?.email}
                                        </p>
                                    </div>
                                </div>

                                <div className="flex items-center gap-3">
                                    {isAdmin && member.role !== 'owner' && member.user_id !== user?.id ? (
                                        <>
                                            <select
                                                value={member.role}
                                                onChange={(e) => updateMemberRole(member.id, e.target.value as OrganizationRole)}
                                                className="text-sm bg-slate-100 dark:bg-slate-700 border-none rounded-lg px-2 py-1"
                                            >
                                                <option value="coach">Coach</option>
                                                <option value="mentor">Mentor</option>
                                                <option value="student">Student</option>
                                            </select>
                                            <button
                                                onClick={() => removeMember(member.id)}
                                                className="p-2 text-red-500 hover:bg-red-50 dark:hover:bg-red-900/20 rounded-lg"
                                                title="Remove member"
                                            >
                                                <UserMinus className="w-4 h-4" />
                                            </button>
                                        </>
                                    ) : (
                                        <span className={`text-xs px-2 py-1 rounded-full ${ROLE_COLORS[member.role]}`}>
                                            {ROLE_LABELS[member.role]}
                                        </span>
                                    )}
                                </div>
                            </div>
                        ))}
                    </div>
                )}
            </div>

            {/* Invite Links (Admin only) */}
            {isAdmin && (
                <div className="bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-700 rounded-xl overflow-hidden">
                    <div className="p-4 border-b border-slate-200 dark:border-slate-700 flex items-center justify-between">
                        <h3 className="font-semibold text-slate-900 dark:text-white flex items-center gap-2">
                            <Link2 className="w-5 h-5 text-slate-500" />
                            Invite Links
                        </h3>
                        <button
                            onClick={() => setShowCreateInvite(true)}
                            className="flex items-center gap-1 text-sm bg-orange-500 hover:bg-orange-600 text-white px-3 py-1.5 rounded-lg"
                        >
                            <UserPlus className="w-4 h-4" />
                            Create Invite
                        </button>
                    </div>

                    {showCreateInvite && (
                        <div className="p-4 bg-slate-50 dark:bg-slate-900/50 border-b border-slate-200 dark:border-slate-700">
                            <div className="grid sm:grid-cols-3 gap-4">
                                <div>
                                    <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-1">
                                        Default Role
                                    </label>
                                    <select
                                        value={newInviteRole}
                                        onChange={(e) => setNewInviteRole(e.target.value as OrganizationRole)}
                                        className="w-full bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-600 rounded-lg px-3 py-2"
                                    >
                                        <option value="student">Student</option>
                                        <option value="mentor">Mentor</option>
                                    </select>
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-1">
                                        Max Uses
                                    </label>
                                    <input
                                        type="number"
                                        value={newInviteMaxUses || ''}
                                        onChange={(e) => setNewInviteMaxUses(e.target.value ? parseInt(e.target.value) : null)}
                                        placeholder="Unlimited"
                                        className="w-full bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-600 rounded-lg px-3 py-2"
                                    />
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-slate-700 dark:text-slate-300 mb-1">
                                        Expires In (days)
                                    </label>
                                    <input
                                        type="number"
                                        value={newInviteExpiresDays || ''}
                                        onChange={(e) => setNewInviteExpiresDays(e.target.value ? parseInt(e.target.value) : null)}
                                        placeholder="Never"
                                        className="w-full bg-white dark:bg-slate-800 border border-slate-200 dark:border-slate-600 rounded-lg px-3 py-2"
                                    />
                                </div>
                            </div>
                            <div className="flex justify-end gap-2 mt-4">
                                <button
                                    onClick={() => setShowCreateInvite(false)}
                                    className="px-4 py-2 text-slate-600 dark:text-slate-400 hover:bg-slate-100 dark:hover:bg-slate-700 rounded-lg"
                                >
                                    Cancel
                                </button>
                                <button
                                    onClick={createInvite}
                                    disabled={isCreatingInvite}
                                    className="flex items-center gap-2 px-4 py-2 bg-orange-500 hover:bg-orange-600 text-white rounded-lg disabled:opacity-50"
                                >
                                    {isCreatingInvite ? (
                                        <Loader2 className="w-4 h-4 animate-spin" />
                                    ) : (
                                        <Check className="w-4 h-4" />
                                    )}
                                    Create
                                </button>
                            </div>
                        </div>
                    )}

                    {invites.length === 0 ? (
                        <div className="p-8 text-center text-slate-500 dark:text-slate-400">
                            No active invite links
                        </div>
                    ) : (
                        <div className="divide-y divide-slate-100 dark:divide-slate-700">
                            {invites.map(invite => (
                                <div
                                    key={invite.id}
                                    className="p-4 flex items-center justify-between"
                                >
                                    <div>
                                        <code className="text-lg font-mono font-bold text-orange-600 dark:text-orange-400">
                                            {invite.invite_code}
                                        </code>
                                        <div className="flex items-center gap-3 mt-1 text-sm text-slate-500 dark:text-slate-400">
                                            <span className={`text-xs px-2 py-0.5 rounded-full ${ROLE_COLORS[invite.default_role]}`}>
                                                {ROLE_LABELS[invite.default_role]}
                                            </span>
                                            <span>
                                                {invite.use_count} / {invite.max_uses || ''} uses
                                            </span>
                                            {invite.expires_at && (
                                                <span>
                                                    Expires: {new Date(invite.expires_at).toLocaleDateString()}
                                                </span>
                                            )}
                                        </div>
                                    </div>
                                    <div className="flex items-center gap-2">
                                        <button
                                            onClick={() => copyInviteLink(invite.invite_code, invite.id)}
                                            className="flex items-center gap-1 px-3 py-1.5 bg-slate-100 hover:bg-slate-200 dark:bg-slate-700 dark:hover:bg-slate-600 text-slate-700 dark:text-slate-200 rounded-lg text-sm"
                                        >
                                            {copiedInviteId === invite.id ? (
                                                <>
                                                    <Check className="w-4 h-4 text-green-500" />
                                                    Copied!
                                                </>
                                            ) : (
                                                <>
                                                    <Copy className="w-4 h-4" />
                                                    Copy Link
                                                </>
                                            )}
                                        </button>
                                        <button
                                            onClick={() => deactivateInvite(invite.id)}
                                            className="p-2 text-red-500 hover:bg-red-50 dark:hover:bg-red-900/20 rounded-lg"
                                            title="Deactivate"
                                        >
                                            <X className="w-4 h-4" />
                                        </button>
                                    </div>
                                </div>
                            ))}
                        </div>
                    )}
                </div>
            )}
        </div>
    );
}
