import React, { createContext, useContext, useEffect, useState, useCallback } from 'react';
import { useAuth } from './auth';
import { supabase } from './supabase';
import type { Organization, OrganizationMember, OrganizationRole, OrganizationInvite } from './database.types';

/**
 * Organization Context
 * Provides the current user's organization membership across the app.
 * Handles multi-org support where users can belong to multiple organizations.
 * 
 * Note: Uses type assertions because the new tables (organizations, organization_members, 
 * organization_invites) are defined in migration 002 which may not be applied yet.
 */

export interface OrganizationMembership {
    organization: Organization;
    membership: OrganizationMember;
}

interface OrganizationContextType {
    // All organizations the user belongs to (approved only)
    organizations: OrganizationMembership[];

    // Pending memberships (awaiting approval)
    pendingOrganizations: OrganizationMembership[];

    // Currently selected/active organization
    currentOrganization: Organization | null;
    currentMembership: OrganizationMember | null;

    // Loading state
    isLoading: boolean;

    // Actions
    setCurrentOrganization: (orgId: string) => void;
    refreshOrganizations: () => Promise<void>;
    createOrganization: (name: string, teamNumber?: string) => Promise<{ organization: Organization | null; error: Error | null }>;
    joinOrganization: (inviteCode: string, role?: OrganizationRole) => Promise<{ success: boolean; error: Error | null }>;
    leaveOrganization: (orgId: string) => Promise<{ success: boolean; error: Error | null }>;

    // Role checks for current organization
    isOwner: boolean;
    isCoach: boolean;
    isAdmin: boolean; // Owner or Coach
    isMentor: boolean;
    isStudent: boolean;
}

const CURRENT_ORG_KEY = 'ftc-current-organization';

const OrganizationContext = createContext<OrganizationContextType | undefined>(undefined);

function getSavedOrgId(): string | null {
    try {
        return localStorage.getItem(CURRENT_ORG_KEY);
    } catch {
        return null;
    }
}

function saveCurrentOrgId(orgId: string | null) {
    try {
        if (orgId) {
            localStorage.setItem(CURRENT_ORG_KEY, orgId);
        } else {
            localStorage.removeItem(CURRENT_ORG_KEY);
        }
    } catch {
        // Ignore storage errors
    }
}

// Helper type for Supabase client with dynamic tables
type SupabaseClient = NonNullable<typeof supabase>;

export function OrganizationProvider({ children }: { children: React.ReactNode }) {
    const { user, isLoading: authLoading, isConfigured } = useAuth();

    const [organizations, setOrganizations] = useState<OrganizationMembership[]>([]);
    const [pendingOrganizations, setPendingOrganizations] = useState<OrganizationMembership[]>([]);
    const [currentOrgId, setCurrentOrgId] = useState<string | null>(getSavedOrgId());
    const [isLoading, setIsLoading] = useState(true);

    // Fetch organizations the user belongs to
    const refreshOrganizations = useCallback(async () => {
        if (!supabase || !user) {
            setOrganizations([]);
            setPendingOrganizations([]);
            setIsLoading(false);
            return;
        }

        try {
            // Use type assertions for new tables
            const client = supabase as unknown as SupabaseClient;

            // Get all memberships with organization data
            const { data: memberships, error } = await (client as any)
                .from('organization_members')
                .select(`
                    *,
                    organization:organizations(*)
                `)
                .eq('user_id', user.id)
                .in('status', ['approved', 'pending']);

            if (error) {
                console.error('Error fetching organizations:', error);
                setIsLoading(false);
                return;
            }

            const approved: OrganizationMembership[] = [];
            const pending: OrganizationMembership[] = [];

            for (const membership of (memberships as any[]) || []) {
                const org = membership.organization as Organization;
                if (!org) continue;

                const item: OrganizationMembership = {
                    organization: org,
                    membership: {
                        id: membership.id,
                        organization_id: membership.organization_id,
                        user_id: membership.user_id,
                        role: membership.role,
                        status: membership.status,
                        approved_at: membership.approved_at,
                        approved_by: membership.approved_by,
                        removed_at: membership.removed_at,
                        removed_by: membership.removed_by,
                        removal_reason: membership.removal_reason,
                        created_at: membership.created_at,
                        updated_at: membership.updated_at,
                    },
                };

                if (membership.status === 'approved') {
                    approved.push(item);
                } else if (membership.status === 'pending') {
                    pending.push(item);
                }
            }

            setOrganizations(approved);
            setPendingOrganizations(pending);

            // Auto-select first org if none selected
            if (!currentOrgId && approved.length > 0) {
                console.log('App: Auto-selecting first organization:', approved[0].organization.name);
                setCurrentOrgId(approved[0].organization.id);
                saveCurrentOrgId(approved[0].organization.id);
                import('./store').then(({ useAppStore }) => {
                    useAppStore.getState().setCurrentOrganizationId(approved[0].organization.id);
                });
            }
        } catch (err) {
            console.error('Error fetching organizations:', err);
        } finally {
            setIsLoading(false);
        }
    }, [user, currentOrgId]);

    // Safety timeout to prevent infinite loading
    useEffect(() => {
        const timer = setTimeout(() => {
            if (isLoading && !authLoading) {
                console.warn('App: Organization loading timed out, forcing completion');
                setIsLoading(false);
            }
        }, 8000);
        return () => clearTimeout(timer);
    }, [isLoading, authLoading]);

    // Load organizations when user changes
    useEffect(() => {
        if (!authLoading && isConfigured) {
            refreshOrganizations();
        } else if (!authLoading) {
            setIsLoading(false);
        }
    }, [user, authLoading, isConfigured, refreshOrganizations]);

    // Clear state on logout
    useEffect(() => {
        if (!authLoading && !user) {
            setOrganizations([]);
            setPendingOrganizations([]);
            setCurrentOrgId(null);
            saveCurrentOrgId(null);
            import('./store').then(({ useAppStore }) => {
                useAppStore.getState().setCurrentOrganizationId(null);
            });
        }
    }, [user, authLoading]);

    // Set current organization
    const setCurrentOrganization = useCallback((orgId: string) => {
        setCurrentOrgId(orgId);
        saveCurrentOrgId(orgId);
        // Sync with global store
        import('./store').then(({ useAppStore }) => {
            useAppStore.getState().setCurrentOrganizationId(orgId);
        });
    }, []);

    // Create a new organization (user becomes owner)
    const createOrganization = useCallback(async (name: string, teamNumber?: string) => {
        if (!supabase || !user) {
            return { organization: null, error: new Error('Not authenticated') };
        }

        try {
            const client = supabase as any;

            // Use the SECURITY DEFINER function to create org + owner atomically
            const { data: newOrgId, error: rpcError } = await client
                .rpc('create_organization_with_owner', {
                    org_name: name,
                    org_team_number: teamNumber || null,
                });

            if (rpcError) throw rpcError;

            // Refresh organizations to get the new org data
            await refreshOrganizations();

            // Set as current organization
            setCurrentOrganization(newOrgId);

            return { organization: { id: newOrgId, name } as Organization, error: null };
        } catch (err) {
            console.error('Error creating organization:', err);
            return { organization: null, error: err as Error };
        }
    }, [user, refreshOrganizations, setCurrentOrganization]);

    // Join an organization via invite code
    const joinOrganization = useCallback(async (inviteCode: string, role?: OrganizationRole) => {
        if (!supabase || !user) {
            return { success: false, error: new Error('Not authenticated') };
        }

        try {
            const client = supabase as any;

            // Find the invite
            const { data: invite, error: inviteError } = await client
                .from('organization_invites')
                .select('*')
                .eq('invite_code', inviteCode)
                .eq('active', true)
                .single() as { data: OrganizationInvite | null; error: any };

            if (inviteError || !invite) {
                return { success: false, error: new Error('Invalid or expired invite code') };
            }

            // Check if invite is expired
            if (invite.expires_at && new Date(invite.expires_at) < new Date()) {
                return { success: false, error: new Error('This invite has expired') };
            }

            // Check if max uses reached
            if (invite.max_uses !== null && invite.use_count >= invite.max_uses) {
                return { success: false, error: new Error('This invite has reached its maximum uses') };
            }

            // Check if already a member
            const { data: existingMember } = await client
                .from('organization_members')
                .select('id, status')
                .eq('organization_id', invite.organization_id)
                .eq('user_id', user.id)
                .single() as { data: { id: string; status: string } | null };

            if (existingMember) {
                if (existingMember.status === 'approved') {
                    return { success: false, error: new Error('You are already a member of this organization') };
                }
                if (existingMember.status === 'pending') {
                    return { success: false, error: new Error('Your membership is pending approval') };
                }
            }

            // Create pending membership
            const { error: memberError } = await client
                .from('organization_members')
                .insert({
                    organization_id: invite.organization_id,
                    user_id: user.id,
                    role: role || invite.default_role || 'student',
                    status: 'pending',
                });

            if (memberError) throw memberError;

            // Increment invite use count
            await client
                .from('organization_invites')
                .update({ use_count: invite.use_count + 1 })
                .eq('id', invite.id);

            // Refresh organizations
            await refreshOrganizations();

            return { success: true, error: null };
        } catch (err) {
            console.error('Error joining organization:', err);
            return { success: false, error: err as Error };
        }
    }, [user, refreshOrganizations]);

    // Leave an organization
    const leaveOrganization = useCallback(async (orgId: string) => {
        if (!supabase || !user) {
            return { success: false, error: new Error('Not authenticated') };
        }

        try {
            const client = supabase as any;

            const { error } = await client
                .from('organization_members')
                .delete()
                .eq('organization_id', orgId)
                .eq('user_id', user.id);

            if (error) throw error;

            // Refresh organizations
            await refreshOrganizations();

            // If leaving current org, switch to another
            if (currentOrgId === orgId) {
                const remaining = organizations.filter(o => o.organization.id !== orgId);
                if (remaining.length > 0) {
                    setCurrentOrganization(remaining[0].organization.id);
                } else {
                    setCurrentOrgId(null);
                    saveCurrentOrgId(null);
                }
            }

            return { success: true, error: null };
        } catch (err) {
            console.error('Error leaving organization:', err);
            return { success: false, error: err as Error };
        }
    }, [user, currentOrgId, organizations, refreshOrganizations, setCurrentOrganization]);

    // Computed values
    const currentOrg = organizations.find(o => o.organization.id === currentOrgId);
    const currentOrganization = currentOrg?.organization || null;
    const currentMembership = currentOrg?.membership || null;

    const isOwner = currentMembership?.role === 'owner';
    const isCoach = currentMembership?.role === 'coach';
    const isAdmin = isOwner || isCoach;
    const isMentor = currentMembership?.role === 'mentor';
    const isStudent = currentMembership?.role === 'student';

    const value: OrganizationContextType = {
        organizations,
        pendingOrganizations,
        currentOrganization,
        currentMembership,
        isLoading: isLoading || authLoading,
        setCurrentOrganization,
        refreshOrganizations,
        createOrganization,
        joinOrganization,
        leaveOrganization,
        isOwner,
        isCoach,
        isAdmin,
        isMentor,
        isStudent,
    };

    return (
        <OrganizationContext.Provider value={value}>
            {children}
        </OrganizationContext.Provider>
    );
}

export function useOrganization() {
    const context = useContext(OrganizationContext);
    if (context === undefined) {
        throw new Error('useOrganization must be used within an OrganizationProvider');
    }
    return context;
}

// Hook to require an organization to be selected
export function useRequireOrganization() {
    const org = useOrganization();
    if (!org.currentOrganization && !org.isLoading) {
        throw new Error('No organization selected');
    }
    return org;
}
